import React, { useState, useMemo, useEffect } from 'react';
import { User } from 'lucide-react';
import type { TrainerDetails } from '@/types';

// Utility function to remove "MT" prefix from trainer names
const removeMTPrefix = (name: string): string => {
  return name.replace(/^MT\s*/i, '').trim();
};

interface TrainerDetailsModalProps {
  trainer: TrainerDetails | null;
  isOpen: boolean;
  onClose: () => void;
}

export const TrainerDetailsModal: React.FC<TrainerDetailsModalProps> = ({
  trainer,
  isOpen,
  onClose,
}) => {
  // State for date range selection
  const [dateRange, setDateRange] = useState<'7d' | '30d' | '90d' | '180d'>('180d');
  // State for animation
  const [isAnimating, setIsAnimating] = useState(false);
  
  useEffect(() => {
    if (isOpen) {
      // Trigger animation after mount
      setTimeout(() => setIsAnimating(true), 10);
    } else {
      setIsAnimating(false);
    }
  }, [isOpen]);
  
  // Filter completion history based on selected date range
  const filteredHistory = useMemo(() => {
    if (!trainer) return [];
    
    const days = dateRange === '7d' ? 7
      : dateRange === '30d' ? 30
      : dateRange === '90d' ? 90
      : 180;
    return trainer.completionHistory.slice(-days);
  }, [trainer, dateRange]);
  
  // Transform history data for the chart
  const chartData = useMemo(() => {
    return filteredHistory.map(point => ({
      date: point.date,
      value: point.completionRate,
    }));
  }, [filteredHistory]);
  
  if (!isOpen || !trainer) return null;
  
  return (
    <div className="fixed inset-0 z-50 overflow-y-auto">
      {/* Backdrop with blur effect */}
      <div
        className={`fixed inset-0 bg-black/30 backdrop-blur-sm transition-opacity duration-300 ${
          isAnimating ? 'opacity-100' : 'opacity-0'
        }`}
        onClick={onClose}
      />
      
      {/* Modal with scale and fade animation */}
      <div className="flex min-h-full items-center justify-center p-4">
        <div
          className={`relative bg-white rounded-lg shadow-2xl max-w-4xl w-full max-h-[90vh] overflow-y-auto transform transition-all duration-300 ease-out scrollbar-hide ${
            isAnimating ? 'scale-100 opacity-100' : 'scale-95 opacity-0'
          }`}
          style={{
            scrollbarWidth: 'none',
            msOverflowStyle: 'none',
          }}
        >
          {/* Close Button - Positioned absolutely */}
          <button
            onClick={onClose}
            className="absolute top-4 right-4 z-20 text-gray-400 hover:text-gray-600 transition-colors bg-white rounded-full p-2 shadow-md hover:shadow-lg"
            aria-label="Close modal"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>

          {/* Profile Header Section */}
          <div className="relative bg-gradient-to-r from-blue-600 to-blue-700 px-6 pt-8 pb-20">
            <div className="flex flex-col items-center">
              {/* Circular Profile Image/Icon */}
              <div className="relative mb-4">
                <div className="w-24 h-24 rounded-full bg-white shadow-lg flex items-center justify-center ring-4 ring-blue-400">
                  <User className="w-12 h-12 text-blue-600" />
                </div>
                {/* Status Badge */}
                <div className="absolute -bottom-1 -right-1 bg-green-500 rounded-full p-1.5 ring-4 ring-white">
                  <svg className="w-3 h-3 text-white" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                  </svg>
                </div>
              </div>
              
              {/* Trainer Name and Title */}
              <h2 className="text-2xl font-bold text-white mb-1">{removeMTPrefix(trainer.trainerName)}</h2>
              <p className="text-blue-100 text-sm font-medium">Master Trainer</p>
            </div>
          </div>
          
          {/* Content with staggered animations */}
          <div className="px-6 -mt-12 pb-6 space-y-6">
            {/* Summary Stats Cards - Elevated above header */}
            <div
              className={`grid grid-cols-2 md:grid-cols-4 gap-4 transition-all duration-500 ${
                isAnimating ? 'translate-y-0 opacity-100' : 'translate-y-5 opacity-0'
              }`}
              style={{ transitionDelay: '100ms' }}
            >
              <div className="bg-white rounded-lg p-4 shadow-lg hover:shadow-xl transition-shadow duration-200 border border-gray-100">
                <div className="flex items-center justify-between mb-2">
                  <p className="text-xs text-gray-500 uppercase tracking-wide font-semibold">Completion</p>
                  <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                    <svg className="w-4 h-4 text-blue-600" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                    </svg>
                  </div>
                </div>
                <p className="text-2xl font-bold text-gray-900">{trainer.completionRate.toFixed(1)}%</p>
              </div>
              
              <div className="bg-white rounded-lg p-4 shadow-lg hover:shadow-xl transition-shadow duration-200 border border-gray-100">
                <div className="flex items-center justify-between mb-2">
                  <p className="text-xs text-gray-500 uppercase tracking-wide font-semibold">Avg Grade</p>
                  <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                    <svg className="w-4 h-4 text-green-600" fill="currentColor" viewBox="0 0 20 20">
                      <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                    </svg>
                  </div>
                </div>
                <p className="text-2xl font-bold text-gray-900">{trainer.averageGrade?.toFixed(1) || 'N/A'}</p>
              </div>
              
              <div className="bg-white rounded-lg p-4 shadow-lg hover:shadow-xl transition-shadow duration-200 border border-gray-100">
                <div className="flex items-center justify-between mb-2">
                  <p className="text-xs text-gray-500 uppercase tracking-wide font-semibold">Teachers</p>
                  <div className="w-8 h-8 bg-purple-100 rounded-full flex items-center justify-center">
                    <svg className="w-4 h-4 text-purple-600" fill="currentColor" viewBox="0 0 20 20">
                      <path d="M9 6a3 3 0 11-6 0 3 3 0 016 0zM17 6a3 3 0 11-6 0 3 3 0 016 0zM12.93 17c.046-.327.07-.66.07-1a6.97 6.97 0 00-1.5-4.33A5 5 0 0119 16v1h-6.07zM6 11a5 5 0 015 5v1H1v-1a5 5 0 015-5z" />
                    </svg>
                  </div>
                </div>
                <p className="text-2xl font-bold text-gray-900">{trainer.teacherCount}</p>
              </div>
              
              <div className="bg-white rounded-lg p-4 shadow-lg hover:shadow-xl transition-shadow duration-200 border border-gray-100">
                <div className="flex items-center justify-between mb-2">
                  <p className="text-xs text-gray-500 uppercase tracking-wide font-semibold">Schools</p>
                  <div className="w-8 h-8 bg-orange-100 rounded-full flex items-center justify-center">
                    <svg className="w-4 h-4 text-orange-600" fill="currentColor" viewBox="0 0 20 20">
                      <path d="M10.394 2.08a1 1 0 00-.788 0l-7 3a1 1 0 000 1.84L5.25 8.051a.999.999 0 01.356-.257l4-1.714a1 1 0 11.788 1.838L7.667 9.088l1.94.831a1 1 0 00.787 0l7-3a1 1 0 000-1.838l-7-3zM3.31 9.397L5 10.12v4.102a8.969 8.969 0 00-1.05-.174 1 1 0 01-.89-.89 11.115 11.115 0 01.25-3.762zM9.3 16.573A9.026 9.026 0 007 14.935v-3.957l1.818.78a3 3 0 002.364 0l5.508-2.361a11.026 11.026 0 01.25 3.762 1 1 0 01-.89.89 8.968 8.968 0 00-5.35 2.524 1 1 0 01-1.4 0zM6 18a1 1 0 001-1v-2.065a8.935 8.935 0 00-2-.712V17a1 1 0 001 1z" />
                    </svg>
                  </div>
                </div>
                <p className="text-2xl font-bold text-gray-900">{trainer.schoolCount}</p>
              </div>
            </div>
            
            {/* Date Range Selector with animation */}
            <div
              className={`flex items-center justify-between transition-all duration-500 ${
                isAnimating ? 'translate-y-0 opacity-100' : 'translate-y-5 opacity-0'
              }`}
              style={{ transitionDelay: '200ms' }}
            >
              <h3 className="text-lg font-semibold text-gray-900">Completion Rate Over Time</h3>
              <div className="flex gap-2">
                <button
                  onClick={() => setDateRange('7d')}
                  className={`px-3 py-1 rounded text-sm font-medium transition-colors ${
                    dateRange === '7d'
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  7 Days
                </button>
                <button
                  onClick={() => setDateRange('30d')}
                  className={`px-3 py-1 rounded text-sm font-medium transition-colors ${
                    dateRange === '30d'
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  30 Days
                </button>
                <button
                  onClick={() => setDateRange('90d')}
                  className={`px-3 py-1 rounded text-sm font-medium transition-colors ${
                    dateRange === '90d'
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  90 Days
                </button>
                <button
                  onClick={() => setDateRange('180d')}
                  className={`px-3 py-1 rounded text-sm font-medium transition-colors ${
                    dateRange === '180d'
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  180 Days
                </button>
              </div>
            </div>
            
            {/* Completion Rate Chart with animation */}
            <div
              className={`bg-gray-50 rounded-lg p-4 transition-all duration-500 ${
                isAnimating ? 'translate-y-0 opacity-100' : 'translate-y-5 opacity-0'
              }`}
              style={{ transitionDelay: '300ms' }}
            >
              <div className="h-64">
                <svg className="w-full h-full" viewBox="0 0 800 250" preserveAspectRatio="none">
                  {/* Grid lines */}
                  <g className="grid-lines">
                    {[0, 25, 50, 75, 100].map((y) => (
                      <line
                        key={y}
                        x1="40"
                        y1={250 - (y * 2)}
                        x2="800"
                        y2={250 - (y * 2)}
                        stroke="#e5e7eb"
                        strokeWidth="1"
                      />
                    ))}
                  </g>
                  
                  {/* Y-axis labels */}
                  <g className="y-axis-labels">
                    {[0, 25, 50, 75, 100].map((y) => (
                      <text
                        key={y}
                        x="30"
                        y={255 - (y * 2)}
                        fontSize="12"
                        fill="#6b7280"
                        textAnchor="end"
                      >
                        {y}%
                      </text>
                    ))}
                  </g>
                  
                  {/* Area chart */}
                  {chartData.length > 0 && (
                    <>
                      <defs>
                        <linearGradient id="areaGradient" x1="0" x2="0" y1="0" y2="1">
                          <stop offset="0%" stopColor="#3b82f6" stopOpacity="0.7" />
                          <stop offset="100%" stopColor="#3b82f6" stopOpacity="0.1" />
                        </linearGradient>
                      </defs>
                      
                      {/* Area path */}
                      <path
                        d={`
                          M 40,250
                          ${chartData.map((point, i) => {
                            const x = 40 + (i * (760 / (chartData.length - 1 || 1)));
                            const y = 250 - (point.value * 2);
                            return `L ${x},${y}`;
                          }).join(' ')}
                          L ${40 + 760},250
                          Z
                        `}
                        fill="url(#areaGradient)"
                      />
                      
                      {/* Line path */}
                      <path
                        d={chartData.map((point, i) => {
                          const x = 40 + (i * (760 / (chartData.length - 1 || 1)));
                          const y = 250 - (point.value * 2);
                          return `${i === 0 ? 'M' : 'L'} ${x},${y}`;
                        }).join(' ')}
                        fill="none"
                        stroke="#3b82f6"
                        strokeWidth="2"
                      />
                    </>
                  )}
                </svg>
              </div>
            </div>
            
            {/* Teachers List with animation */}
            <div
              className={`transition-all duration-500 ${
                isAnimating ? 'translate-y-0 opacity-100' : 'translate-y-5 opacity-0'
              }`}
              style={{ transitionDelay: '400ms' }}
            >
              <div className="mb-4">
                <h3 className="text-lg font-semibold text-gray-900 mb-2">Teachers Performance</h3>
                <div className="bg-blue-50 border border-blue-200 rounded-lg p-3">
                  <p className="text-sm text-blue-800">
                    <span className="font-semibold">Note:</span> These are the top-performing teachers system-wide.
                    Specific teacher-to-master-trainer relationships are not yet available in the API, so this list
                    shows overall top performers rather than teachers specifically assigned to this master trainer.
                  </p>
                </div>
              </div>
              <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Rank
                      </th>
                      <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Teacher Name
                      </th>
                      <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Completion Rate
                      </th>
                      <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Grade
                      </th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {trainer.teachers.map((teacher, index) => (
                      <tr key={teacher.teacherId} className="hover:bg-gray-50 transition-colors duration-150">
                        <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-900">
                          {index + 1}
                        </td>
                        <td className="px-4 py-3 whitespace-nowrap text-sm font-medium text-gray-900">
                          {teacher.teacherName}
                        </td>
                        <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-900">
                          <div className="flex items-center">
                            <div className="w-16 bg-gray-200 rounded-full h-2 mr-2">
                              <div
                                className="bg-blue-600 h-2 rounded-full"
                                style={{ width: `${teacher.completionRate}%` }}
                              />
                            </div>
                            <span>{teacher.completionRate.toFixed(1)}%</span>
                          </div>
                        </td>
                        <td className="px-4 py-3 whitespace-nowrap text-sm">
                          <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                            (teacher.grade || 0) >= 80
                              ? 'bg-green-100 text-green-800'
                              : (teacher.grade || 0) >= 70
                              ? 'bg-yellow-100 text-yellow-800'
                              : 'bg-red-100 text-red-800'
                          }`}>
                            {teacher.grade?.toFixed(1) || 'N/A'}
                          </span>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};