import React, { useMemo } from 'react';
import { Download } from 'lucide-react';
import type { ProcessedTraineeData } from '@/types';
import { exportToCSV } from '@/lib/utils/courseReportUtils';

interface TraineeDataTableProps {
  trainees: ProcessedTraineeData[];
  courseName?: string;
}

export const TraineeDataTable: React.FC<TraineeDataTableProps> = ({
  trainees,
  courseName = 'course',
}) => {
  // Sort trainees by completion rate (descending) and then by final grade
  const sortedTrainees = useMemo(() => {
    return [...trainees].sort((a, b) => {
      if (b.completion !== a.completion) {
        return b.completion - a.completion;
      }
      return b.finalGrade - a.finalGrade;
    });
  }, [trainees]);

  const handleExportCSV = () => {
    const filename = `${courseName.toLowerCase().replace(/\s+/g, '-')}-trainees.csv`;
    exportToCSV(sortedTrainees, filename);
  };

  return (
    <div className="bg-white rounded-lg shadow">
      {/* Header with export button */}
      <div className="px-6 py-4 border-b border-gray-200 flex items-center justify-between">
        <h3 className="text-lg font-semibold text-gray-900">
          Trainee Data ({trainees.length} trainees)
        </h3>
        <button
          onClick={handleExportCSV}
          className="flex items-center gap-2 px-4 py-2 bg-primary text-black rounded-lg hover:bg-primary/90 transition-colors"
        >
          <Download className="h-4 w-4" />
          Export CSV
        </button>
      </div>

      {/* Table */}
      <div className="overflow-x-auto">
        <div className="max-h-[600px] overflow-y-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50 sticky top-0 z-10">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  District
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  School
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  First Name
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Last Name
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Completion
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Final Grade
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {sortedTrainees.length === 0 ? (
                <tr>
                  <td colSpan={6} className="px-6 py-8 text-center text-gray-500">
                    No trainees found matching the selected filters.
                  </td>
                </tr>
              ) : (
                sortedTrainees.map((trainee) => (
                  <tr
                    key={trainee.userId}
                    className="hover:bg-gray-50 transition-colors"
                  >
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {trainee.district}
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-900">
                      <div className="max-w-xs truncate" title={trainee.school}>
                        {trainee.school}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {trainee.firstName}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {trainee.lastName}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm">
                      <span
                        className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                          trainee.completion === 100
                            ? 'bg-green-100 text-green-800'
                            : trainee.completion >= 70
                            ? 'bg-blue-100 text-blue-800'
                            : trainee.completion >= 50
                            ? 'bg-yellow-100 text-yellow-800'
                            : 'bg-red-100 text-red-800'
                        }`}
                      >
                        {trainee.completion.toFixed(2)}%
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm">
                      <span
                        className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                          trainee.finalGrade >= 80
                            ? 'bg-green-100 text-green-800'
                            : trainee.finalGrade >= 60
                            ? 'bg-blue-100 text-blue-800'
                            : trainee.finalGrade >= 40
                            ? 'bg-yellow-100 text-yellow-800'
                            : 'bg-red-100 text-red-800'
                        }`}
                      >
                        {trainee.finalGrade.toFixed(2)}
                      </span>
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Footer with count */}
      {sortedTrainees.length > 0 && (
        <div className="px-6 py-3 bg-gray-50 border-t border-gray-200">
          <p className="text-sm text-gray-600">
            Showing {sortedTrainees.length} trainee{sortedTrainees.length !== 1 ? 's' : ''}
          </p>
        </div>
      )}
    </div>
  );
};