import React, { createContext, useContext, useState, useCallback } from 'react';
import type { FilterState, DateRange, CourseId, CourseReportData } from '@/types';
import { DEFAULT_FILTERS } from '@/lib/constants';

export interface AvailableFilterOptions {
  masterTrainers: string[];
  districts: string[];
  schools: string[];
}

interface FilterContextValue {
  filters: FilterState;
  setDateRange: (range: DateRange | null) => void;
  setDistrictCode: (code: string | null) => void;
  setSearchQuery: (query: string) => void;
  setCourseId: (courseId: CourseId) => void;
  setMasterTrainer: (trainer: string | null) => void;
  setSchool: (school: string | null) => void;
  resetFilters: () => void;
  getAvailableFilterOptions: (courseReportData: CourseReportData | null) => AvailableFilterOptions;
}

const FilterContext = createContext<FilterContextValue | undefined>(undefined);

export const FilterProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [filters, setFilters] = useState<FilterState>(DEFAULT_FILTERS);

  const setDateRange = useCallback((range: DateRange | null) => {
    setFilters(prev => ({ ...prev, dateRange: range }));
  }, []);

  const setDistrictCode = useCallback((code: string | null) => {
    setFilters(prev => ({ ...prev, districtCode: code }));
  }, []);

  const setSearchQuery = useCallback((query: string) => {
    setFilters(prev => ({ ...prev, searchQuery: query }));
  }, []);

  const setCourseId = useCallback((courseId: CourseId) => {
    setFilters(prev => ({ ...prev, courseId }));
  }, []);

  const setMasterTrainer = useCallback((trainer: string | null) => {
    setFilters(prev => ({ ...prev, masterTrainer: trainer }));
  }, []);

  const setSchool = useCallback((school: string | null) => {
    setFilters(prev => ({ ...prev, school }));
  }, []);

  const resetFilters = useCallback(() => {
    setFilters(DEFAULT_FILTERS);
  }, []);

  // Compute available filter options based on current selections
  const getAvailableFilterOptions = useCallback((courseReportData: CourseReportData | null): AvailableFilterOptions => {
    if (!courseReportData) {
      return {
        masterTrainers: [],
        districts: [],
        schools: [],
      };
    }

    const { trainees } = courseReportData;
    const { masterTrainer, districtCode, school } = filters;

    // If no filters are selected, return all options
    if (!masterTrainer && !districtCode && !school) {
      return {
        masterTrainers: courseReportData.filters.masterTrainers,
        districts: courseReportData.filters.districts,
        schools: courseReportData.filters.schools,
      };
    }

    // Compute available options for each filter based on OTHER active filters
    // This creates interdependency between filters
    
    // For Master Trainers: filter by district and school (if selected)
    let traineesForMasterTrainers = trainees;
    if (districtCode) {
      traineesForMasterTrainers = traineesForMasterTrainers.filter(t => t.district === districtCode);
    }
    if (school) {
      traineesForMasterTrainers = traineesForMasterTrainers.filter(t => t.school === school);
    }
    const availableMasterTrainers = Array.from(new Set(traineesForMasterTrainers.map(t => t.group_name))).sort();

    // For Districts: filter by master trainer and school (if selected)
    let traineesForDistricts = trainees;
    if (masterTrainer) {
      traineesForDistricts = traineesForDistricts.filter(t => t.group_name === masterTrainer);
    }
    if (school) {
      traineesForDistricts = traineesForDistricts.filter(t => t.school === school);
    }
    const availableDistricts = Array.from(new Set(traineesForDistricts.map(t => t.district))).sort();

    // For Schools: filter by master trainer and district (if selected)
    let traineesForSchools = trainees;
    if (masterTrainer) {
      traineesForSchools = traineesForSchools.filter(t => t.group_name === masterTrainer);
    }
    if (districtCode) {
      traineesForSchools = traineesForSchools.filter(t => t.district === districtCode);
    }
    const availableSchools = Array.from(new Set(traineesForSchools.map(t => t.school))).sort();

    return {
      masterTrainers: availableMasterTrainers,
      districts: availableDistricts,
      schools: availableSchools,
    };
  }, [filters]);

  const value: FilterContextValue = {
    filters,
    setDateRange,
    setDistrictCode,
    setSearchQuery,
    setCourseId,
    setMasterTrainer,
    setSchool,
    resetFilters,
    getAvailableFilterOptions,
  };

  return (
    <FilterContext.Provider value={value}>
      {children}
    </FilterContext.Provider>
  );
};

export const useFilters = (): FilterContextValue => {
  const context = useContext(FilterContext);
  if (!context) {
    throw new Error('useFilters must be used within FilterProvider');
  }
  return context;
};