import React, { useState, useMemo } from 'react';
import { ChartCard, TrainerDetailsModal } from '@/components/common';
import { formatPercentage } from '@/lib/utils';
import { topN, generateMockTrainerDetails, sortByAverageGrade } from '@/lib/utils/transformers';
import type { Leaderboards, TrainerDetails, TrainerRow, RealApiResponse } from '@/types';
import { useDashboard } from '@/contexts';

// Utility function to remove "MT" prefix from trainer names
const removeMTPrefix = (name: string): string => {
  return name.replace(/^MT\s*/i, '').trim();
};

interface LeaderboardSectionProps {
  leaderboards: Leaderboards;
  searchQuery: string;
}

type LeaderboardTab = 'trainers' | 'districts' | 'schools';

export const LeaderboardSection: React.FC<LeaderboardSectionProps> = ({
  leaderboards,
  searchQuery,
}) => {
  const { rawApiResponses } = useDashboard();
  const [activeTab, setActiveTab] = useState<LeaderboardTab>('trainers');
  const [selectedTrainer, setSelectedTrainer] = useState<TrainerDetails | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const filteredData = useMemo(() => {
    const query = searchQuery.toLowerCase();
    
    return {
      trainers: leaderboards.masterTrainers.filter(t =>
        t.trainerName.toLowerCase().includes(query)
      ),
      districts: leaderboards.districts.filter(d =>
        d.districtName.toLowerCase().includes(query)
      ),
      schools: leaderboards.schools.filter(s =>
        s.schoolName.toLowerCase().includes(query)
      ),
      teachers: leaderboards.teachers.filter(t =>
        t.teacherName.toLowerCase().includes(query)
      ),
    };
  }, [leaderboards, searchQuery]);

  const topTrainers = useMemo(
    () => sortByAverageGrade(topN(filteredData.trainers, t => t.completionRate, 30)),
    [filteredData.trainers]
  );

  const topDistricts = useMemo(
    () => topN(filteredData.districts, d => d.completionRate || 0, 30),
    [filteredData.districts]
  );

  const topSchools = useMemo(
    () => sortByAverageGrade(topN(filteredData.schools, s => s.completionRate || 0, 30)),
    [filteredData.schools]
  );

  const handleTrainerClick = (trainer: TrainerRow) => {
    // Only generate details if rawApiResponses is an array of RealApiResponse
    const details = Array.isArray(rawApiResponses) && rawApiResponses.length > 0 && 'bdtSummary' in rawApiResponses[0]
      ? generateMockTrainerDetails(trainer, rawApiResponses as RealApiResponse[])
      : generateMockTrainerDetails(trainer, []);
    setSelectedTrainer(details);
    setIsModalOpen(true);
  };

  return (
    <ChartCard title="Top 10 Leaderboard" description="Best performing entities">
      <div className="space-y-4">
        {/* Tabs */}
        <div className="flex gap-2 border-b border-gray-200">
          {[
            { key: 'trainers' as const, label: 'Master Trainers' },
            { key: 'schools' as const, label: 'Schools' },
            { key: 'districts' as const, label: 'Districts' },
          ].map(tab => (
            <button
              key={tab.key}
              onClick={() => setActiveTab(tab.key)}
              className={`px-4 py-2 font-medium transition-colors ${
                activeTab === tab.key
                  ? 'text-primary border-b-2 border-primary'
                  : 'text-gray-600 hover:text-gray-900'
              }`}
            >
              {tab.label}
            </button>
          ))}
        </div>

        {/* Table */}
        <div className="overflow-x-auto">
          {activeTab === 'trainers' && (
            <table className="w-full">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Rank
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Master Trainer
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Teachers
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Schools
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Avg Grade
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Completion Rate
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {topTrainers.map((trainer, idx) => {
                  const avgGrade = trainer.averageGrade;
                  const gradeBadgeClass = avgGrade
                    ? avgGrade >= 80
                      ? 'bg-green-100 text-green-800'
                      : avgGrade >= 70
                      ? 'bg-yellow-100 text-yellow-800'
                      : 'bg-red-100 text-red-800'
                    : 'bg-gray-100 text-gray-800';

                  return (
                    <tr
                      key={trainer.trainerId}
                      className="hover:bg-gray-50 cursor-pointer"
                      onClick={() => handleTrainerClick(trainer)}
                    >
                      <td className="px-4 py-3 text-sm text-gray-900">{idx + 1}</td>
                      <td className="px-4 py-3 text-sm font-medium text-gray-900">{removeMTPrefix(trainer.trainerName)}</td>
                      <td className="px-4 py-3 text-sm text-gray-900">{trainer.teacherCount || 0}</td>
                      <td className="px-4 py-3 text-sm text-gray-900">{trainer.schoolCount || 0}</td>
                      <td className="px-4 py-3 text-sm">
                        <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${gradeBadgeClass}`}>
                          {avgGrade ? avgGrade.toFixed(1) : 'N/A'}
                        </span>
                      </td>
                      <td className="px-4 py-3 text-sm font-medium text-success">
                        {formatPercentage(trainer.completionRate)}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          )}

          {activeTab === 'districts' && (
            <table className="w-full">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">Rank</th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">District</th>
                  <th className="px-4 py-3 text-right text-sm font-semibold text-gray-900">Completion Rate</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {topDistricts.map((district, idx) => (
                  <tr key={district.districtCode} className="hover:bg-gray-50">
                    <td className="px-4 py-3 text-sm text-gray-900">{idx + 1}</td>
                    <td className="px-4 py-3 text-sm font-medium text-gray-900">{district.districtName}</td>
                    <td className="px-4 py-3 text-sm text-right font-medium text-success">
                      {formatPercentage(district.completionRate || 0)}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}

          {activeTab === 'schools' && (
            <table className="w-full">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Rank</th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">School</th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Avg Grade</th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Completion Rate</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {topSchools.map((school, idx) => {
                  const avgGrade = school.averageGrade;
                  const gradeBadgeClass = avgGrade
                    ? avgGrade >= 80
                      ? 'bg-green-100 text-green-800'
                      : avgGrade >= 70
                      ? 'bg-yellow-100 text-yellow-800'
                      : 'bg-red-100 text-red-800'
                    : 'bg-gray-100 text-gray-800';

                  return (
                    <tr key={school.schoolId} className="hover:bg-gray-50">
                      <td className="px-4 py-3 text-sm text-gray-900">{idx + 1}</td>
                      <td className="px-4 py-3 text-sm font-medium text-gray-900">{school.schoolName}</td>
                      <td className="px-4 py-3 text-sm">
                        <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${gradeBadgeClass}`}>
                          {avgGrade ? avgGrade.toFixed(1) : 'N/A'}
                        </span>
                      </td>
                      <td className="px-4 py-3 text-sm font-medium text-success">
                        {formatPercentage(school.completionRate || 0)}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          )}
        </div>
      </div>

      <TrainerDetailsModal
        trainer={selectedTrainer}
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
      />
    </ChartCard>
  );
};