import type {
  Totals,
  TimePoint,
  DistrictRow,
  ChartDataPoint,
  RealApiResponse,
  DashboardPayload,
  TrainerRow,
  SchoolRow,
  TeacherRow,
  TrainerDetails,
  TeacherWithGrade,
  CompletionHistoryPoint,
  LeaderboardItem
} from '@/types';

/**
 * Transform real API response to internal dashboard payload format
 */
export const transformApiResponse = (apiData: RealApiResponse[]): DashboardPayload => {
  // Find the entry with the latest date
  const latestData = apiData.reduce((latest, current) => {
    return current.currentdate > latest.currentdate ? current : latest;
  }, apiData[0]);
  
  const data = latestData;
  const summary = data.bdtSummary;

  // Calculate totals
  const traineesTotal = summary.Trainees;
  const completionRate = parseFloat(summary.AverageCompetionRate);
  const certifiedTotal = Math.round((completionRate / 100) * traineesTotal);
  const averageFinalGrade = parseFloat(summary.AverageFinalGrade);

  const totals: Totals = {
    traineesTotal,
    certifiedTotal,
    completionRate,
    averageFinalGrade
  };

  // Transform districts from TopCompletionDistricts
  const districts: DistrictRow[] = summary.TopCompletionDistricts.map((item, index) => ({
    districtCode: `DISTRICT_${index}`,
    districtName: item.group || 'Unknown',
    certified: 0, // Not provided in real API
    completionRate: parseFloat(item.average)
  }));

  // Transform master trainers with mock data for new fields
  const masterTrainers: TrainerRow[] = summary.TopCompletionMasterTrainers.map((item, index) => {
    const schoolCount = Math.floor(Math.random() * 5) + 3; // 3-7 schools
    const teacherCount = Math.floor(Math.random() * 11) + 10; // 10-20 teachers
    const averageGrade = Math.random() * 20 + 70; // 70-90 average grade
    
    return {
      trainerId: `MT-${String(index + 1).padStart(3, '0')}`,
      trainerName: item.group,
      completionRate: parseFloat(item.average),
      schoolCount,
      teacherCount,
      averageGrade
    };
  });

  // Transform schools from both TopGradesSchools and TopCompletionSchools
  const schoolsMap = new Map<string, SchoolRow>();
  
  // Add schools from TopGradesSchools
  summary.TopGradesSchools.forEach((item, index) => {
    if (item.group && item.group !== 'unknown' && item.group !== '') {
      schoolsMap.set(item.group, {
        schoolId: `SCH-${String(index + 1).padStart(4, '0')}`,
        schoolName: item.group,
        averageGrade: parseFloat(item.average),
        completionRate: 0
      });
    }
  });

  // Update with completion rates from TopCompletionSchools
  summary.TopCompletionSchools.forEach((item) => {
    if (item.group && item.group !== 'unknown' && item.group !== '') {
      const existing = schoolsMap.get(item.group);
      if (existing) {
        existing.completionRate = parseFloat(item.average);
      } else {
        schoolsMap.set(item.group, {
          schoolId: `SCH-${String(schoolsMap.size + 1).padStart(4, '0')}`,
          schoolName: item.group,
          completionRate: parseFloat(item.average)
        });
      }
    }
  });

  const schools = Array.from(schoolsMap.values());

  // Transform teachers
  const teachers: TeacherRow[] = summary.TopCompletionTeachers
    .filter(item => item.group && item.group !== '')
    .map((item, index) => ({
      teacherId: `TCH-${String(index + 1).padStart(3, '0')}`,
      teacherName: item.group,
      completionRate: parseFloat(item.average)
    }));

  // Generate mock time series data (since real API doesn't provide it)
  const timeSeries: TimePoint[] = generateMockTimeSeries(traineesTotal, certifiedTotal);

  return {
    meta: {
      generatedAt: new Date(data.currentdate).toISOString()
    },
    totals,
    districts,
    timeSeries,
    leaderboards: {
      masterTrainers,
      districts: districts.slice(0, 30), // Top 30 districts
      schools: schools.slice(0, 50), // Top 50 schools
      teachers: teachers.slice(0, 50) // Top 50 teachers
    }
  };
};

/**
 * Generate mock time series data based on totals
 * This creates a realistic progression over 90 days
 */
const generateMockTimeSeries = (traineesTotal: number, certifiedTotal: number): TimePoint[] => {
  const days = 90;
  const timeSeries: TimePoint[] = [];
  const today = new Date();
  
  for (let i = days - 1; i >= 0; i--) {
    const date = new Date(today);
    date.setDate(date.getDate() - i);
    const dateStr = date.toISOString().split('T')[0];
    
    // Calculate cumulative certified with a sigmoid-like growth curve
    const progress = (days - i) / days;
    const cumulativeCertified = Math.round(certifiedTotal * (1 / (1 + Math.exp(-10 * (progress - 0.5)))));
    
    // Calculate daily certified (difference from previous day)
    const prevCumulative = i === days - 1 ? 0 : timeSeries[timeSeries.length - 1]?.cumulativeCertified || 0;
    const dailyCertified = cumulativeCertified - prevCumulative;
    
    timeSeries.push({
      date: dateStr,
      dailyCertified,
      cumulativeCertified,
      traineesTotal,
      certifiedTotal: cumulativeCertified
    });
  }
  
  return timeSeries;
};

/**
 * Calculate completion rate from totals
 * @param t - Totals object containing traineesTotal and certifiedTotal
 * @returns Completion rate as a percentage (0-100)
 */
export const toCompletionRate = (t: Totals): number => {
  return t.completionRate || (t.traineesTotal ? (t.certifiedTotal / t.traineesTotal) * 100 : 0);
};

/**
 * Calculate completion rate for a specific district
 * @param district - District row containing trainees and certified counts
 * @returns Completion rate as a percentage (0-100)
 */
export const toDistrictCompletionRate = (district: DistrictRow): number => {
  return district.completionRate || (district.trainees ? (district.certified / district.trainees) * 100 : 0);
};

/**
 * Transform time series data to completion rate series
 * @param ts - Array of time points
 * @returns Array of chart data points with date and completion rate
 */
export const toRateSeries = (ts: TimePoint[]): ChartDataPoint[] => {
  return ts.map(p => ({
    x: p.date,
    y: p.certifiedTotal && p.traineesTotal 
      ? (p.certifiedTotal / p.traineesTotal) * 100 
      : 0
  }));
};

/**
 * Transform time series data to cumulative certified series
 * @param ts - Array of time points
 * @returns Array of chart data points with date and cumulative certified count
 */
export const toCumulativeSeries = (ts: TimePoint[]): ChartDataPoint[] => {
  return ts.map(p => ({
    x: p.date,
    y: p.cumulativeCertified ?? p.certifiedTotal ?? 0
  }));
};

/**
 * Transform time series data to daily certified series
 * @param ts - Array of time points
 * @returns Array of chart data points with date and daily certified count
 */
export const toDailySeries = (ts: TimePoint[]): ChartDataPoint[] => {
  return ts.map(p => ({
    x: p.date,
    y: p.dailyCertified ?? 0
  }));
};
/**
 * Transform time series data to daily rate change series
 * Calculates the day-over-day percentage point change in completion rate
 * @param ts - Array of time points
 * @returns Array of chart data points with date and percentage point change
 */
export const toDailyRateChangeSeries = (ts: TimePoint[]): ChartDataPoint[] => {
  if (!ts || ts.length === 0) {
    return [];
  }

  return ts.map((p, index) => {
    // Calculate current day's completion rate
    const currentRate = p.certifiedTotal && p.traineesTotal && p.traineesTotal > 0
      ? (p.certifiedTotal / p.traineesTotal) * 100
      : 0;

    // For the first day, change is 0 (no previous day to compare)
    if (index === 0) {
      return {
        x: p.date,
        y: 0
      };
    }

    // Calculate previous day's completion rate
    const prevPoint = ts[index - 1];
    const prevRate = prevPoint.certifiedTotal && prevPoint.traineesTotal && prevPoint.traineesTotal > 0
      ? (prevPoint.certifiedTotal / prevPoint.traineesTotal) * 100
      : 0;

    // Calculate the percentage point change
    const change = currentRate - prevRate;

    return {
      x: p.date,
      y: change
    };
  });
};


/**
 * Get top N items from an array based on a key function
 * @param arr - Array of items to filter
 * @param key - Function to extract the numeric value to sort by
 * @param n - Number of top items to return (default: 10)
 * @returns Array of top N items sorted in descending order
 */
export const topN = <T>(
  arr: T[],
  key: (v: T) => number,
  n = 10
): T[] => {
  return [...arr].sort((a, b) => key(b) - key(a)).slice(0, n);
};

/**
 * Transform district data for bar chart visualization
 * @param districts - Array of district rows
 * @returns Object with categories (district names) and data (certified counts)
 */
export const toDistrictBarData = (districts: DistrictRow[]) => {
  const sorted = [...districts].sort((a, b) =>
    a.districtName.localeCompare(b.districtName)
  );
  return {
    categories: sorted.map(d => d.districtName),
    data: sorted.map(d => d.completionRate || 0)
  };
};

/**
 * Transform totals data for pie chart visualization
 * @param totals - Totals object containing traineesTotal and certifiedTotal
 * @returns Object with series (values) and labels for pie chart
 */
export const toPieData = (totals: Totals) => {
  return {
    series: [totals.certifiedTotal, totals.traineesTotal - totals.certifiedTotal],
    labels: ['Certified', 'Not Certified']
  };
};

/**
 * Extract historical completion rate data for a specific trainer from all API responses
 * @param apiResponses - Array of all API responses (122 entries with different dates)
 * @param trainerName - Name of the trainer to extract history for
 * @returns Array of completion history points sorted by date
 */
export function extractTrainerHistory(
  apiResponses: RealApiResponse[],
  trainerName: string
): CompletionHistoryPoint[] {
  const history: CompletionHistoryPoint[] = [];
  
  // Iterate through all API responses (122 entries)
  for (const response of apiResponses) {
    const date = new Date(response.currentdate).toISOString().split('T')[0];
    
    // Find this trainer in TopCompletionMasterTrainers
    const trainerData = response.bdtSummary.TopCompletionMasterTrainers?.find(
      (t: LeaderboardItem) => t.group === trainerName
    );
    
    if (trainerData) {
      const completionRate = parseFloat(trainerData.average);
      
      history.push({
        date: date,
        completionRate: completionRate,
        certifiedCount: 0, // Not available in current data
        traineeCount: 0,   // Not available in current data
      });
    }
  }
  
  // Sort by date chronologically
  return history.sort((a, b) => a.date.localeCompare(b.date));
}

/**
 * Generate trainer details for the trainer details modal using real teacher data
 * @param trainer - Trainer row to generate details for
 * @param apiResponses - Array of all API responses for extracting real historical data
 * @returns TrainerDetails object with REAL teachers and REAL completion history
 */
export function generateMockTrainerDetails(
  trainer: TrainerRow,
  apiResponses: RealApiResponse[]
): TrainerDetails {
  // Extract REAL historical completion rate data
  const completionHistory = extractTrainerHistory(apiResponses, trainer.trainerName);
  
  // Get the latest API response to extract real teacher data
  const latestData = apiResponses.reduce((latest, current) => {
    return current.currentdate > latest.currentdate ? current : latest;
  }, apiResponses[0]);
  
  // Extract real teachers from TopCompletionTeachers and TopGradesTeachers
  const topCompletionTeachers = latestData.bdtSummary.TopCompletionTeachers || [];
  const topGradesTeachers = latestData.bdtSummary.TopGradesTeachers || [];
  
  // Create a map to combine completion and grade data for teachers
  const teacherMap = new Map<string, TeacherWithGrade>();
  
  // Add teachers from TopCompletionTeachers
  topCompletionTeachers.forEach((item, index) => {
    if (item.group && item.group !== '') {
      teacherMap.set(item.group, {
        teacherId: `TCH-${String(index + 1).padStart(3, '0')}`,
        teacherName: item.group,
        completionRate: parseFloat(item.average),
        masterTrainerId: trainer.trainerId,
      });
    }
  });
  
  // Update with grades from TopGradesTeachers
  topGradesTeachers.forEach((item: LeaderboardItem) => {
    if (item.group && item.group !== '') {
      const existing = teacherMap.get(item.group);
      if (existing) {
        existing.grade = parseFloat(item.average);
      } else {
        teacherMap.set(item.group, {
          teacherId: `TCH-${String(teacherMap.size + 1).padStart(3, '0')}`,
          teacherName: item.group,
          completionRate: 0,
          grade: parseFloat(item.average),
          masterTrainerId: trainer.trainerId,
        });
      }
    }
  });
  
  // Convert map to array and sort by grade (descending)
  const teachers = Array.from(teacherMap.values())
    .sort((a, b) => (b.grade || 0) - (a.grade || 0));
  
  // Calculate average grade from real teacher data
  const averageGrade = teachers.length > 0
    ? teachers.reduce((sum, t) => sum + (t.grade || 0), 0) / teachers.length
    : trainer.averageGrade || 0;
  
  return {
    trainerId: trainer.trainerId,
    trainerName: trainer.trainerName,
    districtCode: trainer.districtCode,
    completionRate: trainer.completionRate,
    averageGrade: averageGrade,
    schoolCount: trainer.schoolCount || Math.floor(Math.random() * 5) + 3,
    teacherCount: teachers.length,
    teachers: teachers,
    completionHistory: completionHistory,
  };
}

/**
 * Sort items by average grade in descending order
 * @param items - Array of items with optional averageGrade property
 * @returns Sorted array with highest grades first
 */
export function sortByAverageGrade<T extends { averageGrade?: number }>(
  items: T[]
): T[] {
  return [...items].sort((a, b) => {
    const gradeA = a.averageGrade ?? 0;
    const gradeB = b.averageGrade ?? 0;
    return gradeB - gradeA;
  });
}