import React from 'react';
import ReactApexChart from 'react-apexcharts';
import type { ApexOptions } from 'apexcharts';
import type { DistributionRange } from '@/types';
import { CHART_DEFAULTS } from '@/lib/constants';

interface DistributionChartProps {
  title: string;
  data: DistributionRange[];
  color?: string;
  height?: number;
}

export const DistributionChart: React.FC<DistributionChartProps> = ({
  title,
  data,
  color = '#3b82f6',
  height = 300,
}) => {
  const chartOptions: ApexOptions = {
    chart: {
      type: 'bar',
      height,
      fontFamily: CHART_DEFAULTS.fontFamily,
      toolbar: CHART_DEFAULTS.toolbar,
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: '70%',
        borderRadius: 4,
        dataLabels: {
          position: 'top',
        },
      },
    },
    dataLabels: {
      enabled: true,
      formatter: (val: number) => val.toString(),
      offsetY: -20,
      style: {
        fontSize: '12px',
        colors: ['#304758'],
      },
    },
    xaxis: {
      categories: data.map(d => d.range),
      title: {
        text: 'Range (%)',
        style: {
          fontSize: '12px',
          fontWeight: 600,
        },
      },
      labels: {
        style: {
          fontSize: '11px',
        },
      },
    },
    yaxis: {
      title: {
        text: 'Number of Trainees',
        style: {
          fontSize: '12px',
          fontWeight: 600,
        },
      },
      labels: {
        formatter: (val: number) => Math.floor(val).toString(),
        style: {
          fontSize: '11px',
        },
      },
    },
    colors: [color],
    title: {
      text: title,
      align: 'left',
      style: {
        fontSize: '16px',
        fontWeight: 600,
        color: '#1f2937',
      },
    },
    grid: {
      borderColor: '#e5e7eb',
      strokeDashArray: 4,
    },
    tooltip: {
      y: {
        formatter: (val: number, opts) => {
          const percentage = data[opts.dataPointIndex]?.percentage || 0;
          return `${val} trainees (${percentage.toFixed(1)}%)`;
        },
      },
    },
  };

  const series = [
    {
      name: 'Trainees',
      data: data.map(d => d.count),
    },
  ];

  return (
    <div className="bg-white rounded-lg shadow p-6">
      <ReactApexChart
        options={chartOptions}
        series={series}
        type="bar"
        height={height}
      />
    </div>
  );
};