import React from 'react';
import type { Course, CourseId } from '@/types';

interface CourseNavigationTabsProps {
  courses: Course[];
  selectedCourseId: CourseId;
  onCourseChange: (courseId: CourseId) => void;
}

export const CourseNavigationTabs: React.FC<CourseNavigationTabsProps> = ({
  courses,
  selectedCourseId,
  onCourseChange,
}) => {
  return (
    <div className="border-b border-gray-200 bg-white">
      <nav
        className="flex space-x-8 overflow-x-auto scrollbar-hide px-6"
        aria-label="Course navigation"
        role="tablist"
      >
        {courses.map((course) => {
          const isActive = course.id === selectedCourseId;
          
          return (
            <button
              key={course.slug}
              onClick={() => onCourseChange(course.id)}
              role="tab"
              aria-selected={isActive}
              aria-controls={`course-panel-${course.slug}`}
              className={`
                whitespace-nowrap py-4 px-3 border-b-2 font-medium text-sm transition-colors
                ${
                  isActive
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }
              `}
            >
              {course.name}
            </button>
          );
        })}
      </nav>
    </div>
  );
};