import type { 
  DashboardPayload, 
  CourseId, 
  CourseApiResponse, 
  CourseTraineeRecord,
  Totals,
  DistrictRow,
  Leaderboards,
  TrainerRow,
  SchoolRow,
  TeacherRow,
  TimePoint
} from '@/types';

/**
 * Transform course-specific API response to dashboard format
 * 
 * This function takes the raw trainee records from the course API
 * and aggregates them into the dashboard format used by the UI.
 */
export function transformCourseApiResponse(
  courseData: CourseApiResponse
): DashboardPayload {
  // Calculate totals
  const totals = calculateTotals(courseData);
  
  // Aggregate by district
  const districts = aggregateByDistrict(courseData);
  
  // Create leaderboards
  const leaderboards = createLeaderboards(courseData);
  
  // Generate time series (placeholder - would need historical data)
  const timeSeries = generateTimeSeries(courseData);
  
  return {
    meta: {
      generatedAt: new Date().toISOString(),
    },
    totals,
    districts,
    timeSeries,
    leaderboards,
  };
}

/**
 * Calculate overall totals from trainee records
 */
function calculateTotals(records: CourseTraineeRecord[]): Totals {
  const totalTrainees = records.length;
  const completedTrainees = records.filter(r => parseFloat(r.completion_rate) === 100).length;
  
  // Calculate average completion rate
  const totalCompletionRate = records.reduce((sum, r) => sum + parseFloat(r.completion_rate), 0);
  const avgCompletionRate = totalTrainees > 0 ? totalCompletionRate / totalTrainees : 0;
  
  // Calculate average final grade
  const totalGrade = records.reduce((sum, r) => sum + parseFloat(r.final_grade), 0);
  const avgFinalGrade = totalTrainees > 0 ? totalGrade / totalTrainees : 0;
  
  return {
    traineesTotal: totalTrainees,
    certifiedTotal: completedTrainees,
    completionRate: avgCompletionRate,
    averageFinalGrade: avgFinalGrade,
  };
}

/**
 * Aggregate trainee data by district
 */
function aggregateByDistrict(records: CourseTraineeRecord[]): DistrictRow[] {
  const districtMap = new Map<string, {
    trainees: number;
    certified: number;
    totalCompletion: number;
  }>();
  
  records.forEach(record => {
    const district = record.district;
    const existing = districtMap.get(district) || { trainees: 0, certified: 0, totalCompletion: 0 };
    
    existing.trainees++;
    if (parseFloat(record.completion_rate) === 100) {
      existing.certified++;
    }
    existing.totalCompletion += parseFloat(record.completion_rate);
    
    districtMap.set(district, existing);
  });
  
  return Array.from(districtMap.entries()).map(([district, data]) => ({
    districtCode: district,
    districtName: district,
    trainees: data.trainees,
    certified: data.certified,
    completionRate: data.trainees > 0 ? data.totalCompletion / data.trainees : 0,
  }));
}

/**
 * Create leaderboards from trainee records
 */
function createLeaderboards(records: CourseTraineeRecord[]): Leaderboards {
  // Aggregate by master trainer
  const trainerMap = new Map<string, {
    traineeCount: number;
    certifiedCount: number;
    totalCompletion: number;
    totalGrade: number;
    districts: Set<string>;
    schools: Set<string>;
  }>();
  
  records.forEach(record => {
    const trainer = record.group_name;
    const existing = trainerMap.get(trainer) || {
      traineeCount: 0,
      certifiedCount: 0,
      totalCompletion: 0,
      totalGrade: 0,
      districts: new Set<string>(),
      schools: new Set<string>(),
    };
    
    existing.traineeCount++;
    if (parseFloat(record.completion_rate) === 100) {
      existing.certifiedCount++;
    }
    existing.totalCompletion += parseFloat(record.completion_rate);
    existing.totalGrade += parseFloat(record.final_grade);
    existing.districts.add(record.district);
    existing.schools.add(record.school);
    
    trainerMap.set(trainer, existing);
  });
  
  const masterTrainers: TrainerRow[] = Array.from(trainerMap.entries())
    .map(([trainer, data]) => ({
      trainerId: trainer,
      trainerName: trainer,
      schoolCount: data.schools.size,
      traineeCount: data.traineeCount,
      certifiedCount: data.certifiedCount,
      completionRate: data.traineeCount > 0 ? data.totalCompletion / data.traineeCount : 0,
      averageGrade: data.traineeCount > 0 ? data.totalGrade / data.traineeCount : 0,
      teacherCount: data.traineeCount,
    }))
    .sort((a, b) => b.completionRate - a.completionRate);
  
  // Aggregate by school
  const schoolMap = new Map<string, {
    district: string;
    traineeCount: number;
    certifiedCount: number;
    totalCompletion: number;
    totalGrade: number;
  }>();
  
  records.forEach(record => {
    const school = record.school;
    const existing = schoolMap.get(school) || {
      district: record.district,
      traineeCount: 0,
      certifiedCount: 0,
      totalCompletion: 0,
      totalGrade: 0,
    };
    
    existing.traineeCount++;
    if (parseFloat(record.completion_rate) === 100) {
      existing.certifiedCount++;
    }
    existing.totalCompletion += parseFloat(record.completion_rate);
    existing.totalGrade += parseFloat(record.final_grade);
    
    schoolMap.set(school, existing);
  });
  
  const schools: SchoolRow[] = Array.from(schoolMap.entries())
    .map(([school, data]) => ({
      schoolId: school,
      schoolName: school,
      districtCode: data.district,
      traineeCount: data.traineeCount,
      certifiedCount: data.certifiedCount,
      completionRate: data.traineeCount > 0 ? data.totalCompletion / data.traineeCount : 0,
      averageGrade: data.traineeCount > 0 ? data.totalGrade / data.traineeCount : 0,
    }))
    .sort((a, b) => (b.completionRate || 0) - (a.completionRate || 0));
  
  // Create teacher records
  const teachers: TeacherRow[] = records.map(record => ({
    teacherId: record.user_id,
    teacherName: `${record.firstname} ${record.lastname}`,
    completionRate: parseFloat(record.completion_rate),
    grade: parseFloat(record.final_grade),
    masterTrainerId: record.group_name,
    trainerId: record.group_name,
  })).sort((a, b) => b.completionRate - a.completionRate);
  
  // Get districts from the aggregated data
  const districts = aggregateByDistrict(records)
    .sort((a, b) => (b.completionRate || 0) - (a.completionRate || 0));
  
  return {
    masterTrainers,
    districts,
    schools,
    teachers,
  };
}

/**
 * Generate time series data (placeholder)
 * Note: This would require historical data which isn't available in the current API
 */
function generateTimeSeries(records: CourseTraineeRecord[]): TimePoint[] {
  const today = new Date().toISOString().split('T')[0];
  const completedCount = records.filter(r => parseFloat(r.completion_rate) === 100).length;
  
  return [{
    date: today,
    dailyCertified: completedCount,
    cumulativeCertified: completedCount,
    traineesTotal: records.length,
    certifiedTotal: completedCount,
  }];
}

