/**
 * Transform and filter course data
 * 
 * This function combines course filtering with any other data transformations.
 * It's designed to be the main entry point for course-aware data processing.
 * 
 * @param data - The raw dashboard data
 * @param courseId - The course ID to filter by
 * @returns Transformed and filtered dashboard data
 */
export function transformCourseData(
  data: DashboardPayload | null,
  courseId: CourseId
): DashboardPayload | null {
  if (!data) return null;
  
  // Apply course filtering
  const filteredData = filterByCourse(data, courseId);
  
  // Additional transformations can be added here as needed
  // For example: data normalization, calculated fields, etc.
  
  return filteredData;
}