import React, { useEffect, useState } from 'react';
import { useSearchParams } from 'react-router-dom';
import { CheckCircle, AlertCircle, Award, Calendar, GraduationCap, Mail, MapPin, School, User, Hash } from 'lucide-react';
import { dashboardService } from '@/lib/api/dashboardService';
import { LoadingSpinner, ErrorMessage } from '@/components/common';
import type { CertificateData } from '@/types';

export const CertificateVerificationPage: React.FC = () => {
  const [searchParams] = useSearchParams();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [certificateData, setCertificateData] = useState<CertificateData | null>(null);

  const courseId = searchParams.get('courseId');
  const email = searchParams.get('email');

  useEffect(() => {
    const fetchCertificate = async () => {
      // Validate required parameters
      if (!courseId || !email) {
        setError('Missing required parameters. Please provide both courseId and email.');
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        setError(null);
        
        const data = await dashboardService.fetchCertificateVerification(courseId, email);
        console.log(` DEBUG Component: Received data:`, data);
        console.log(` DEBUG Component: finalGrade type:`, typeof data.finalGrade, 'value:', data.finalGrade);
        setCertificateData(data);
      } catch (err) {
        console.error('Error fetching certificate:', err);
        
        // Extract and display the specific error message from the API
        let errorMessage = 'Failed to verify certificate. Please check your parameters and try again.';
        
        if (err instanceof Error) {
          errorMessage = err.message;
        }
        
        // Add context information to help users understand what was attempted
        const contextInfo = `\n\nVerification attempted with:\n Course ID: ${courseId}\n Email: ${email}`;
        setError(errorMessage + contextInfo);
      } finally {
        setLoading(false);
      }
    };

    fetchCertificate();
  }, [courseId, email]);

  // Loading state
  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <LoadingSpinner size="lg" />
          <p className="mt-4 text-gray-600">Verifying certificate...</p>
        </div>
      </div>
    );
  }

  // Error state
  if (error || !certificateData) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
        <div className="max-w-md w-full">
          <ErrorMessage 
            message={error || 'Certificate not found'} 
            onRetry={() => window.location.reload()}
          />
        </div>
      </div>
    );
  }

  // Success state - Display certificate
  return (
    <div className="min-h-screen bg-gray-50 py-8 px-4 sm:px-6 lg:px-8">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="text-center mb-8">
          <div className="flex justify-center mb-4">
            <div className="bg-green-100 rounded-full p-3">
              <CheckCircle className="h-12 w-12 text-green-600" />
            </div>
          </div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            Certificate Verification
          </h1>
          <p className="text-gray-600">
            This certificate has been verified and is authentic
          </p>
        </div>

        {/* Certificate Card */}
        <div className="bg-white rounded-lg shadow-lg overflow-hidden border-2 border-green-500 print:border-gray-300">
          {/* Verified Badge */}
          <div className="bg-green-500 text-white py-3 px-6 flex items-center justify-center">
            <Award className="h-5 w-5 mr-2" />
            <span className="font-semibold">VERIFIED CERTIFICATE</span>
          </div>

          {/* Certificate Content */}
          <div className="p-8 space-y-6">
            {/* Trainee Information */}
            <div className="border-b border-gray-200 pb-6">
              <h2 className="text-xl font-semibold text-gray-900 mb-4 flex items-center">
                <User className="h-5 w-5 mr-2 text-gray-600" />
                Trainee Information
              </h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <InfoItem 
                  icon={<User className="h-4 w-4" />}
                  label="Full Name"
                  value={certificateData.traineeName}
                />
                <InfoItem 
                  icon={<Mail className="h-4 w-4" />}
                  label="Email"
                  value={certificateData.email}
                />
                <InfoItem 
                  icon={<Hash className="h-4 w-4" />}
                  label="National ID"
                  value={certificateData.nid}
                />
                <InfoItem 
                  icon={<MapPin className="h-4 w-4" />}
                  label="District"
                  value={certificateData.district}
                />
                <InfoItem 
                  icon={<School className="h-4 w-4" />}
                  label="School"
                  value={certificateData.school}
                  className="md:col-span-2"
                />
              </div>
            </div>

            {/* Course Information */}
            <div className="border-b border-gray-200 pb-6">
              <h2 className="text-xl font-semibold text-gray-900 mb-4 flex items-center">
                <GraduationCap className="h-5 w-5 mr-2 text-gray-600" />
                Course Information
              </h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <InfoItem 
                  icon={<GraduationCap className="h-4 w-4" />}
                  label="Course Name"
                  value={certificateData.courseName}
                  className="md:col-span-2"
                />
                <InfoItem 
                  icon={<Hash className="h-4 w-4" />}
                  label="Course Code"
                  value={certificateData.courseShortname}
                />
                <InfoItem 
                  icon={<Award className="h-4 w-4" />}
                  label="Final Grade"
                  value={`${certificateData.finalGrade.toFixed(1)}%`}
                  valueClassName={certificateData.finalGrade >= 70 ? 'text-green-600 font-semibold' : 'text-gray-900'}
                />
              </div>
            </div>

            {/* Certificate Details */}
            <div className="border-b border-gray-200 pb-6">
              <h2 className="text-xl font-semibold text-gray-900 mb-4 flex items-center">
                <Award className="h-5 w-5 mr-2 text-gray-600" />
                Certificate Details
              </h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <InfoItem 
                  icon={<Hash className="h-4 w-4" />}
                  label="Certificate ID"
                  value={certificateData.certificateId}
                  className="md:col-span-2"
                  valueClassName="font-mono text-sm"
                />
                <InfoItem 
                  icon={<Calendar className="h-4 w-4" />}
                  label="Completion Date"
                  value={certificateData.completionDate}
                />
              </div>
            </div>

            {/* Module Information */}
            {certificateData.modules && certificateData.modules.length > 0 && (
              <div>
                <h2 className="text-xl font-semibold text-gray-900 mb-4">
                  Course Modules
                </h2>
                <div className="space-y-2">
                  {certificateData.modules.map((module, index) => (
                    <div 
                      key={index}
                      className="flex items-center justify-between p-3 bg-gray-50 rounded-lg"
                    >
                      <div className="flex items-center">
                        <CheckCircle className="h-4 w-4 text-green-600 mr-2" />
                        <span className="text-gray-900">{module.module_name}</span>
                      </div>
                      <div className="flex items-center space-x-4">
                        <span className="text-sm text-gray-600">
                          {module.completion_status}
                        </span>
                        {module.grade !== undefined && (
                          <span className="text-sm font-semibold text-gray-900">
                            {module.grade}%
                          </span>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>

          {/* Footer */}
          <div className="bg-gray-50 px-8 py-4 border-t border-gray-200">
            <div className="flex items-center justify-between text-sm text-gray-600">
              <div className="flex items-center">
                <AlertCircle className="h-4 w-4 mr-2" />
                <span>Issued by TVET AmaliTech</span>
              </div>
              <button
                onClick={() => window.print()}
                className="px-4 py-2 bg-primary text-white rounded-lg hover:bg-primary/90 transition-colors print:hidden"
              >
                Print Certificate
              </button>
            </div>
          </div>
        </div>

        {/* Verification Info */}
        <div className="mt-6 text-center text-sm text-gray-600 print:hidden">
          <p>
            This certificate can be verified at any time by visiting this page with the same parameters.
          </p>
          <p className="mt-2">
            For questions or concerns, please contact TVET AmaliTech support.
          </p>
        </div>
      </div>
    </div>
  );
};

// Helper component for displaying information items
interface InfoItemProps {
  icon: React.ReactNode;
  label: string;
  value: string;
  className?: string;
  valueClassName?: string;
}

const InfoItem: React.FC<InfoItemProps> = ({ 
  icon, 
  label, 
  value, 
  className = '',
  valueClassName = 'text-gray-900'
}) => {
  return (
    <div className={`${className}`}>
      <div className="flex items-center text-sm text-gray-600 mb-1">
        {icon}
        <span className="ml-2">{label}</span>
      </div>
      <p className={`text-base font-medium ${valueClassName}`}>
        {value || 'N/A'}
      </p>
    </div>
  );
};