import React from 'react';
import { ChartCard } from '@/components/common';
import { AreaChart } from '@/components/charts';
import { toCumulativeSeries, toDailyRateChangeSeries } from '@/lib/utils/transformers';
import { formatNumber } from '@/lib/utils';
import { CHART_COLORS } from '@/lib/constants';
import type { TimePoint } from '@/types';

interface TimeSeriesSectionProps {
  timeSeries: TimePoint[];
}

export const TimeSeriesSection: React.FC<TimeSeriesSectionProps> = ({
  timeSeries,
}) => {
  const formatRateChange = (val: number): string => {
    const sign = val > 0 ? '+' : val < 0 ? '' : '';
    return `${sign}${val.toFixed(1)} pp`;
  };

  return (
    <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
      <ChartCard
        title="Course Completion Over Time"
        description="Cumulative certified trainees"
      >
        <AreaChart
          data={timeSeries}
          seriesName="Certified Trainees"
          yAxisTitle="Cumulative Certified"
          dataTransformer={toCumulativeSeries}
          valueFormatter={formatNumber}
          color={CHART_COLORS.success}
        />
      </ChartCard>

      <ChartCard
        title="Daily Certification Rate Change"
        description="Day-over-day percentage point changes in certification rate"
      >
        <AreaChart
          data={timeSeries}
          seriesName="Rate Change"
          yAxisTitle="Percentage Points (pp)"
          dataTransformer={toDailyRateChangeSeries}
          valueFormatter={formatRateChange}
          color="#10b981"
        />
      </ChartCard>
    </div>
  );
};