import { BrowserRouter, Routes, Route, useSearchParams } from 'react-router-dom';
import { useEffect } from 'react';
import { DashboardProvider, FilterProvider, useFilters } from './contexts';
import { DashboardPage } from './features/dashboard/DashboardPage';
import { CertificateVerificationPage } from './features/certificate';
import { COURSES } from './lib/constants';

/**
 * Wrapper component that syncs URL parameters with FilterContext
 * This enables shareable URLs with course selection
 */
function DashboardPageWrapper() {
  const [searchParams, setSearchParams] = useSearchParams();
  const { filters, setCourseId } = useFilters();

  // Sync URL course parameter to FilterContext on mount
  useEffect(() => {
    const courseParam = searchParams.get('course');
    if (courseParam) {
      const validCourse = COURSES.find(c => c.slug === courseParam);
      if (validCourse && validCourse.id !== filters.courseId) {
        setCourseId(validCourse.id);
      }
    }
  }, []); // Only run on mount

  // Sync FilterContext course changes to URL
  useEffect(() => {
    const currentCourse = COURSES.find(c => c.id === filters.courseId);
    const currentParam = searchParams.get('course');
    
    if (currentCourse) {
      if (currentCourse.id === null) {
        // Remove course param for "All Courses"
        if (currentParam) {
          searchParams.delete('course');
          setSearchParams(searchParams, { replace: true });
        }
      } else if (currentCourse.slug !== currentParam) {
        // Update course param
        searchParams.set('course', currentCourse.slug);
        setSearchParams(searchParams, { replace: true });
      }
    }
  }, [filters.courseId]);

  return <DashboardPage />;
}

function App() {
  return (
    <BrowserRouter basename="/custom">
      <FilterProvider>
        <DashboardProvider>
          <Routes>
            <Route path="/" element={<DashboardPageWrapper />} />
            <Route path="/verify-certificate" element={<CertificateVerificationPage />} />
          </Routes>
        </DashboardProvider>
      </FilterProvider>
    </BrowserRouter>
  );
}

export default App;
