import { useMemo } from 'react';
import type { DashboardPayload, FilterState } from '@/types';
import { filterByCourse } from '@/lib/utils/courseTransformers';

export const useFilteredData = (
  data: DashboardPayload | null,
  filters: FilterState
): DashboardPayload | null => {
  return useMemo(() => {
    if (!data) return null;

    // Apply course filter first (prepared for future API enhancement)
    // Currently returns data unchanged as API doesn't provide course-specific data
    let courseFilteredData = filterByCourse(data, filters.courseId);
    if (!courseFilteredData) return null;

    let filteredTimeSeries = courseFilteredData.timeSeries;
    let filteredDistricts = courseFilteredData.districts;
    let filteredTotals = courseFilteredData.totals;

    // Apply date range filter
    if (filters.dateRange) {
      filteredTimeSeries = filteredTimeSeries.filter(point => {
        const date = point.date;
        return date >= filters.dateRange!.startDate && date <= filters.dateRange!.endDate;
      });
    }

    // Apply district filter
    if (filters.districtCode) {
      filteredDistricts = filteredDistricts.filter(
        d => d.districtCode === filters.districtCode
      );

      // Recalculate totals based on filtered districts
      const totalCertified = filteredDistricts.reduce((sum, d) => sum + d.certified, 0);
      const totalTrainees = filteredDistricts.reduce((sum, d) => sum + (d.trainees || 0), 0);
      
      filteredTotals = {
        ...courseFilteredData.totals,
        certifiedTotal: totalCertified,
        traineesTotal: totalTrainees,
        completionRate: totalTrainees > 0 ? (totalCertified / totalTrainees) * 100 : 0,
      };

      // Scale time series data proportionally based on the filtered district's share
      // Since time series data doesn't contain district information, we scale it
      // based on the ratio of the filtered district's certified count to the total
      const scaleFactor = courseFilteredData.totals.certifiedTotal > 0
        ? totalCertified / courseFilteredData.totals.certifiedTotal
        : 0;

      filteredTimeSeries = filteredTimeSeries.map(point => ({
        ...point,
        dailyCertified: point.dailyCertified ? Math.round(point.dailyCertified * scaleFactor) : 0,
        cumulativeCertified: point.cumulativeCertified ? Math.round(point.cumulativeCertified * scaleFactor) : 0,
        traineesTotal: totalTrainees,
        certifiedTotal: point.certifiedTotal ? Math.round(point.certifiedTotal * scaleFactor) : 0,
      }));
    }

    return {
      ...courseFilteredData,
      timeSeries: filteredTimeSeries,
      districts: filteredDistricts,
      totals: filteredTotals,
    };
  }, [data, filters]);
};