import React from 'react';
import { useDashboard, useFilters } from '@/contexts';
import { useFilteredData } from '@/lib/hooks';
import { ErrorMessage, ProgressBar } from '@/components/common';
import { SearchFilter, DistrictFilter, CourseNavigationTabs } from '@/components/filters';
import { CompletionOverview, TimeSeriesSection, LeaderboardSection } from '@/components/sections';
import { IndividualCourseView } from './IndividualCourseView';
import { formatDate } from '@/lib/utils';
import { COURSES } from '@/lib/constants';

export const DashboardPage: React.FC = () => {
  const { data, loading, error, courseReportData, currentCourse, loadingProgress } = useDashboard();
  const { filters, setDistrictCode, setSearchQuery, setCourseId, resetFilters } = useFilters();
  const filteredData = useFilteredData(data, filters);

  // Get selected course name for display
  const selectedCourse = COURSES.find(course => course.id === filters.courseId);
  const courseDisplayName = selectedCourse?.name || 'All Courses';
  
  // Check if we're viewing an individual course
  const isIndividualCourseView = currentCourse !== null && courseReportData !== null;

  // Show error state
  if (error && !data) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <ErrorMessage message={error} />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <p className="text-sm text-gray-600">
            {courseDisplayName} {data && ` Last updated: ${formatDate(data.meta.generatedAt, 'long')}`}
          </p>
        </div>
      </header>

      {/* Course Navigation */}
      <CourseNavigationTabs
        courses={COURSES}
        selectedCourseId={filters.courseId}
        onCourseChange={setCourseId}
      />

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {loading === 'loading' ? (
          /* Show progress bar while loading */
          <div className="flex items-center justify-center min-h-[60vh]">
            <ProgressBar
              progress={loadingProgress}
              message={`Loading ${courseDisplayName} data...`}
            />
          </div>
        ) : data && isIndividualCourseView ? (
          /* Individual Course View */
          <IndividualCourseView
            courseReportData={courseReportData}
            courseName={courseDisplayName}
          />
        ) : data ? (
          /* All Courses View */
          <>
            {/* Filters */}
            <div className="bg-white rounded-lg shadow mb-6 p-4">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <SearchFilter
                  value={filters.searchQuery}
                  onChange={setSearchQuery}
                  placeholder="Search leaderboards..."
                />
                <DistrictFilter
                  districts={data.districts}
                  value={filters.districtCode}
                  onChange={setDistrictCode}
                />
                <button
                  onClick={resetFilters}
                  className="px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
                >
                  Reset Filters
                </button>
              </div>
            </div>

            <div className="space-y-8">
              {/* Completion Overview */}
              <CompletionOverview
                totals={filteredData?.totals || data.totals}
                districts={filteredData?.districts || data.districts}
              />

              {/* Time Series */}
              <TimeSeriesSection
                timeSeries={filteredData?.timeSeries || data.timeSeries}
              />

              {/* Leaderboards */}
              <LeaderboardSection
                leaderboards={data.leaderboards}
                searchQuery={filters.searchQuery}
              />
            </div>
          </>
        ) : null}
      </main>
    </div>
  );
};