import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import type {
  DashboardPayload,
  LoadingState,
  RealApiResponse,
  CourseApiResponse,
  CourseId,
  CourseTraineeRecord,
  CourseReportData
} from '@/types';
import { dashboardService } from '@/lib/api';
import { useFilters } from './FilterContext';
import { COURSES } from '@/lib/constants';
import { processCourseReportData } from '@/lib/utils/courseReportUtils';

interface DashboardContextValue {
  data: DashboardPayload | null;
  rawApiResponses: RealApiResponse[] | CourseApiResponse;
  courseReportData: CourseReportData | null;
  loading: LoadingState;
  loadingProgress: number;
  error: string | null;
  refetch: () => Promise<void>;
  currentCourse: CourseId;
}

const DashboardContext = createContext<DashboardContextValue | undefined>(undefined);

export const DashboardProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [data, setData] = useState<DashboardPayload | null>(null);
  const [rawApiResponses, setRawApiResponses] = useState<RealApiResponse[] | CourseApiResponse>([]);
  const [courseReportData, setCourseReportData] = useState<CourseReportData | null>(null);
  const [loading, setLoading] = useState<LoadingState>('idle');
  const [loadingProgress, setLoadingProgress] = useState<number>(0);
  const [error, setError] = useState<string | null>(null);
  const { filters } = useFilters();
  const currentCourse = filters.courseId;

  const fetchData = useCallback(async () => {
    setLoading('loading');
    setLoadingProgress(0);
    setError(null);
    
    const startTime = Date.now();
    let progressInterval: number | null = null;
    
    // Adaptive progress simulation that responds to actual loading time
    const updateProgress = () => {
      setLoadingProgress(prev => {
        const elapsed = Date.now() - startTime;
        
        // For fast loads (< 5 seconds), progress quickly to 90%
        if (elapsed < 5000) {
          if (prev < 90) return Math.min(prev + 15, 90);
          return prev;
        }
        
        // For medium loads (5-30 seconds), progress steadily
        if (elapsed < 30000) {
          if (prev < 30) return prev + 5;
          if (prev < 60) return prev + 3;
          if (prev < 85) return prev + 1.5;
          if (prev < 90) return prev + 0.5;
          return prev;
        }
        
        // For long loads (> 30 seconds), slow down to avoid reaching 100%
        if (prev < 30) return prev + 3;
        if (prev < 60) return prev + 2;
        if (prev < 85) return prev + 1;
        if (prev < 92) return prev + 0.3;
        return prev;
      });
    };
    
    progressInterval = setInterval(updateProgress, 500); // Update every 500ms for smoother progress
    
    try {
      let result;
      
      // Determine which API to call based on selected course
      if (currentCourse === null) {
        // Fetch "All Courses" data
        console.log(' Fetching All Courses data');
        result = await dashboardService.fetchDashboardData();
      } else {
        // Fetch course-specific data
        const course = COURSES.find(c => c.id === currentCourse);
        if (!course?.apiCourseId) {
          throw new Error(`Invalid course: ${currentCourse}`);
        }
        console.log(` Fetching data for course: ${course.name} (API ID: ${course.apiCourseId})`);
        result = await dashboardService.fetchCourseData(course.apiCourseId);
      }
      
      setData(result.data);
      setRawApiResponses(result.rawApiResponses);
      
      // Process course report data if we have individual course data
      if (currentCourse !== null && Array.isArray(result.rawApiResponses)) {
        const traineeRecords = result.rawApiResponses as CourseTraineeRecord[];
        const reportData = processCourseReportData(traineeRecords);
        setCourseReportData(reportData);
      } else {
        setCourseReportData(null);
      }
      
      // Clear progress interval and smoothly complete to 100%
      if (progressInterval) clearInterval(progressInterval);
      setLoadingProgress(100);
      setLoading('success');
    } catch (err) {
      if (progressInterval) clearInterval(progressInterval);
      setLoadingProgress(0);
      setError(err instanceof Error ? err.message : 'An error occurred');
      setLoading('error');
      console.error('Error fetching dashboard data:', err);
    }
  }, [currentCourse]);

  // Fetch data when component mounts or when course changes
  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const value: DashboardContextValue = {
    data,
    rawApiResponses,
    courseReportData,
    loading,
    loadingProgress,
    error,
    refetch: fetchData,
    currentCourse,
  };

  return (
    <DashboardContext.Provider value={value}>
      {children}
    </DashboardContext.Provider>
  );
};

export const useDashboard = (): DashboardContextValue => {
  const context = useContext(DashboardContext);
  if (!context) {
    throw new Error('useDashboard must be used within DashboardProvider');
  }
  return context;
};