import React, { useMemo } from 'react';
import Chart from 'react-apexcharts';
import type { ApexOptions } from 'apexcharts';
import { CHART_COLORS, CHART_DEFAULTS } from '@/lib/constants';
import type { DistrictRow } from '@/types';
import { toDistrictBarData } from '@/lib/utils/transformers';

interface BarChartProps {
  data: DistrictRow[];
  height?: number;
}

export const BarChart: React.FC<BarChartProps> = ({ data, height = 400 }) => {
  const chartData = useMemo(() => toDistrictBarData(data), [data]);

  const options: ApexOptions = {
    chart: {
      type: 'bar',
      fontFamily: CHART_DEFAULTS.fontFamily,
      toolbar: CHART_DEFAULTS.toolbar,
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: '70%',
        borderRadius: 4,
      },
    },
    dataLabels: {
      enabled: false,
    },
    xaxis: {
      categories: chartData.categories,
      labels: {
        rotate: -45,
        rotateAlways: true,
        style: {
          fontSize: '11px',
        },
      },
    },
    yaxis: {
      title: {
        text: 'Completion Rate (%)',
      },
      labels: {
        formatter: (val: number) => `${val.toFixed(1)}%`,
      },
    },
    colors: [CHART_COLORS.district],
    tooltip: {
      y: {
        formatter: (val: number) => `${val.toFixed(1)}%`,
      },
    },
    grid: {
      borderColor: '#f1f1f1',
    },
  };

  return (
    <Chart
      options={options}
      series={[{ name: 'Certified', data: chartData.data }]}
      type="bar"
      height={height}
    />
  );
};