import type { DashboardPayload, RealApiResponse, CourseApiResponse, CertificateVerificationResponse, CertificateData } from '@/types';
import { transformApiResponse } from '@/lib/utils/transformers';
import { transformCourseApiResponse } from '@/lib/utils/courseTransformers';

// API endpoint configuration
const API_ENDPOINT = import.meta.env.VITE_API_BASE_URL
  ? `${import.meta.env.VITE_API_BASE_URL}${import.meta.env.VITE_DASHBOARD_ENDPOINT || '/getdashboardsummary'}`
  : 'https://tvet.amalitech.org/api/getdashboardsummary';

// Course-specific API endpoint
const COURSE_API_ENDPOINT = 'https://tvet.amalitech.org/registration/updatedata.php';

// Certificate verification API endpoint
// In production: use live API directly
// In development: use relative URL to leverage Vite proxy for CORS handling
const CERTIFICATE_API_ENDPOINT = import.meta.env.MODE === 'production'
  ? 'https://tvet.amalitech.org/registration/updatedata.php'
  : '/phpapi/index.php';

// Fallback to local data for development
const FALLBACK_DATA_PATH = '/data/dashboard_summary.json';
const USE_LOCAL_DATA = import.meta.env.VITE_USE_LOCAL_DATA === 'true';

export interface DashboardServiceResponse {
  data: DashboardPayload;
  rawApiResponses: RealApiResponse[] | CourseApiResponse;
}

export class DashboardService {
  private static instance: DashboardService;
  private cache: DashboardServiceResponse | null = null;
  private cacheTimestamp: number = 0;
  private courseCache: Map<string, DashboardServiceResponse> = new Map();
  private courseCacheTimestamps: Map<string, number> = new Map();
  private readonly CACHE_DURATION = 5 * 60 * 1000; // 5 minutes
  private readonly MAX_RETRIES = 3;
  private readonly RETRY_DELAY = 1000; // 1 second

  private constructor() {}

  static getInstance(): DashboardService {
    if (!DashboardService.instance) {
      DashboardService.instance = new DashboardService();
    }
    return DashboardService.instance;
  }

  /**
   * Fetch dashboard data from the live API endpoint with retry logic
   */
  async fetchDashboardData(): Promise<DashboardServiceResponse> {
    const now = Date.now();
    
    // Return cached data if still valid
    if (this.cache && (now - this.cacheTimestamp) < this.CACHE_DURATION) {
      return this.cache;
    }

    // Use local data if configured for development
    if (USE_LOCAL_DATA) {
      console.log(' Using LOCAL data source (VITE_USE_LOCAL_DATA=true)');
      return this.fetchLocalData();
    }

    // Try to fetch from API with retries
    
    for (let attempt = 1; attempt <= this.MAX_RETRIES; attempt++) {
      try {
        const data = await this.fetchFromApi();
        this.cache = data;
        this.cacheTimestamp = now;
        return data;
      } catch (error) {
        
        console.warn(`API fetch attempt ${attempt}/${this.MAX_RETRIES} failed:`, error);
        
        // Wait before retrying (except on last attempt)
        if (attempt < this.MAX_RETRIES) {
          await this.delay(this.RETRY_DELAY * attempt);
        }
      }
    }

    // All retries failed, try local data
    console.error('All API fetch attempts failed, trying local data');
    try {
      return await this.fetchLocalData();
    } catch (localDataError) {
      console.error('Local data fetch also failed:', localDataError);
      throw new Error(
        `Failed to load dashboard data from server after ${this.MAX_RETRIES} attempts. ` +
        'Please check your connection and try again.'
      );
    }
  }

  /**
   * Fetch course-specific dashboard data from the API
   */
  async fetchCourseData(courseId: string): Promise<DashboardServiceResponse> {
    const now = Date.now();
    
    // Return cached data if still valid
    const cachedData = this.courseCache.get(courseId);
    const cacheTimestamp = this.courseCacheTimestamps.get(courseId) || 0;
    
    if (cachedData && (now - cacheTimestamp) < this.CACHE_DURATION) {
      console.log(` Using cached data for course ${courseId}`);
      return cachedData;
    }

    // Use local data if configured for development
    if (USE_LOCAL_DATA) {
      console.log(` Using LOCAL data source for course ${courseId} (VITE_USE_LOCAL_DATA=true)`);
      return this.fetchLocalData();
    }

    // Try to fetch from API with retries
    
    for (let attempt = 1; attempt <= this.MAX_RETRIES; attempt++) {
      try {
        const data = await this.fetchCourseFromApi(courseId);
        this.courseCache.set(courseId, data);
        this.courseCacheTimestamps.set(courseId, now);
        return data;
      } catch (error) {
        
        console.warn(`Course API fetch attempt ${attempt}/${this.MAX_RETRIES} failed:`, error);
        
        // Wait before retrying (except on last attempt)
        if (attempt < this.MAX_RETRIES) {
          await this.delay(this.RETRY_DELAY * attempt);
        }
      }
    }

    // All retries failed, try local data
    console.error(`All course API fetch attempts failed for course ${courseId}, trying local data`);
    try {
      return await this.fetchLocalData();
    } catch (localDataError) {
      console.error('Local data fetch also failed:', localDataError);
      throw new Error(
        `Failed to load course data from server after ${this.MAX_RETRIES} attempts. ` +
        'Please check your connection and try again.'
      );
    }
  }

  /**
   * Fetch data from the live API endpoint
   */
  private async fetchFromApi(): Promise<DashboardServiceResponse> {
    const timeoutMs = Number(import.meta.env.VITE_API_TIMEOUT) || 60000; // Default 60 seconds
    const response = await fetch(API_ENDPOINT, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      // Add timeout to prevent hanging requests
      signal: AbortSignal.timeout(timeoutMs),
    });
    
    if (!response.ok) {
      throw new Error(`API request failed: ${response.status} ${response.statusText}`);
    }
    
    const realApiData: RealApiResponse[] = await response.json();
    
    // Transform to internal format and return both transformed and raw data
    return {
      data: transformApiResponse(realApiData),
      rawApiResponses: realApiData
    };
  }

  /**
   * Fetch course-specific data from the API
   */
  private async fetchCourseFromApi(courseId: string): Promise<DashboardServiceResponse> {
    const timeoutMs = Number(import.meta.env.VITE_API_TIMEOUT) || 60000; // Default 60 seconds
    
    console.log(` Fetching course data for courseId: ${courseId}`);
    console.log(` API Endpoint: ${COURSE_API_ENDPOINT}`);
    console.log(` Request body:`, { action: 'get_progress', courseId: courseId });
    
    const response = await fetch(COURSE_API_ENDPOINT, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        action: 'get_progress',
        courseId: courseId,
      }),
      mode: 'cors',
      signal: AbortSignal.timeout(timeoutMs),
    });
    
    console.log(` Response status: ${response.status} ${response.statusText}`);
    console.log(` Response headers:`, Object.fromEntries(response.headers.entries()));
    
    if (!response.ok) {
      const errorText = await response.text();
      console.error(` Course API request failed: ${response.status} ${response.statusText}`);
      console.error(` Error response body:`, errorText);
      throw new Error(`Course API request failed: ${response.status} ${response.statusText}`);
    }
    
    const responseText = await response.text();
    console.log(` Raw response (first 500 chars):`, responseText.substring(0, 500));
    
    let courseApiData: CourseApiResponse;
    try {
      courseApiData = JSON.parse(responseText);
      console.log(` Course data loaded successfully for courseId: ${courseId}. Records count:`, courseApiData.length);
      console.log(` Sample record:`, courseApiData[0]);
    } catch (parseError) {
      console.error(` Failed to parse JSON response:`, parseError);
      console.error(` Response text:`, responseText);
      throw new Error(`Failed to parse API response: ${parseError}`);
    }
    
    // Transform course-specific data to dashboard format
    return {
      data: transformCourseApiResponse(courseApiData),
      rawApiResponses: courseApiData
    };
  }

  /**
   * Fetch data from local JSON file
   */
  private async fetchLocalData(): Promise<DashboardServiceResponse> {
    console.log(' Fetching data from local file:', FALLBACK_DATA_PATH);
    const response = await fetch(FALLBACK_DATA_PATH);
    if (!response.ok) {
      throw new Error(`Failed to fetch local data: ${response.statusText}`);
    }
    
    const realApiData: RealApiResponse[] = await response.json();
    console.log(' Local data loaded successfully. Records count:', realApiData.length);
    
    // Transform to internal format and return both transformed and raw data
    return {
      data: transformApiResponse(realApiData),
      rawApiResponses: realApiData
    };
  }

  /**
   * Utility function to delay execution
   */
  private delay(ms: number): Promise<void> {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  /**
   * Clear the cache to force a fresh fetch
   */
  clearCache(): void {
    this.cache = null;
    this.cacheTimestamp = 0;
    this.courseCache.clear();
    this.courseCacheTimestamps.clear();
  }

  /**
   * Clear cache for a specific course
   */
  clearCourseCache(courseId: string): void {
    this.courseCache.delete(courseId);
    this.courseCacheTimestamps.delete(courseId);
  }

  /**
   * Fetch certificate verification data from the API
   */
  async fetchCertificateVerification(
    courseId: string,
    email: string
  ): Promise<CertificateData> {
    const timeoutMs = Number(import.meta.env.VITE_API_TIMEOUT) || 60000;
    
    console.log(` Fetching certificate verification for courseId: ${courseId}, email: ${email}`);
    console.log(` API Endpoint: ${CERTIFICATE_API_ENDPOINT}`);
    
    const url = `${CERTIFICATE_API_ENDPOINT}?action=verify_certificate&courseId=${encodeURIComponent(courseId)}&email=${encodeURIComponent(email)}`;
    
    const response = await fetch(url, {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
      },
      signal: AbortSignal.timeout(timeoutMs),
    });
    
    console.log(` Response status: ${response.status} ${response.statusText}`);
    
    if (!response.ok) {
      // Try to parse error response as JSON to get specific error message
      let errorMessage = `Certificate verification failed: ${response.status} ${response.statusText}`;
      try {
        const errorData = await response.json();
        if (errorData.message) {
          errorMessage = errorData.message;
        }
      } catch (parseError) {
        // If JSON parsing fails, try to get text
        try {
          const errorText = await response.text();
          if (errorText) {
            errorMessage = errorText;
          }
        } catch (textError) {
          // Use default error message
        }
      }
      console.error(` Certificate verification request failed:`, errorMessage);
      throw new Error(errorMessage);
    }
    
    const responseData: CertificateVerificationResponse = await response.json();
    console.log(` Certificate verification response:`, responseData);
    console.log(` DEBUG: final_grade type:`, typeof responseData.final_grade, 'value:', responseData.final_grade);
    console.log(` DEBUG: Full response data:`, JSON.stringify(responseData, null, 2));
    
    // Check if there's an error in the response
    if (responseData.error) {
      const errorMessage = responseData.message || 'Certificate not found or invalid';
      console.error(` Certificate verification error:`, errorMessage);
      throw new Error(errorMessage);
    }
    
    // Transform API response to internal format
    if (!responseData.trainee_name || !responseData.certificate_id) {
      throw new Error('Invalid certificate data received from server');
    }
    
    return {
      traineeName: responseData.trainee_name,
      email: responseData.email || '',
      nid: responseData.nid || '',
      district: responseData.district || '',
      school: responseData.school || '',
      courseName: responseData.course_name || '',
      courseShortname: responseData.course_shortname || '',
      finalGrade: parseFloat(String(responseData.final_grade || 0)),
      certificateId: responseData.certificate_id,
      completionDate: responseData.completion_date || '',
      modules: responseData.modules || [],
    };
  }
}

export const dashboardService = DashboardService.getInstance();