import React from 'react';

// Utility function to remove "MT" prefix from trainer names
const removeMTPrefix = (name: string): string => {
  return name.replace(/^MT\s*/i, '').trim();
};

interface MasterTrainerFilterProps {
  masterTrainers: string[];
  value: string | null;
  onChange: (value: string | null) => void;
}

export const MasterTrainerFilter: React.FC<MasterTrainerFilterProps> = ({
  masterTrainers,
  value,
  onChange,
}) => {
  return (
    <div className="flex flex-col gap-1">
      <label className="text-sm font-medium text-gray-700">
        Master Trainers ({masterTrainers.length}):
      </label>
      <select
        value={value || ''}
        onChange={(e) => onChange(e.target.value || null)}
        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary focus:border-transparent"
      >
        <option value="">All</option>
        {masterTrainers.map(trainer => (
          <option key={trainer} value={trainer}>
            {removeMTPrefix(trainer)}
          </option>
        ))}
      </select>
    </div>
  );
};