import React, { useState, useEffect } from 'react';
import { cn } from '@/lib/utils';

interface ProgressBarProps {
  progress: number; // 0-100
  message?: string;
  className?: string;
}

export const ProgressBar: React.FC<ProgressBarProps> = ({
  progress,
  message = 'Loading dashboard data...',
  className,
}) => {
  const [showSlowLoadingMessage, setShowSlowLoadingMessage] = useState(false);
  
  // Ensure progress is between 0 and 100
  const clampedProgress = Math.min(Math.max(progress, 0), 100);
  
  // Show "taking longer" message after 10 seconds
  useEffect(() => {
    const timer = setTimeout(() => {
      if (clampedProgress < 100) {
        setShowSlowLoadingMessage(true);
      }
    }, 10000); // 10 seconds
    
    return () => clearTimeout(timer);
  }, [clampedProgress]);
  
  // Reset slow loading message when progress completes
  useEffect(() => {
    if (clampedProgress === 100) {
      setShowSlowLoadingMessage(false);
    }
  }, [clampedProgress]);
  
  return (
    <div className={cn('w-full max-w-2xl mx-auto', className)}>
      <div className="bg-white rounded-lg shadow-lg p-6">
        {/* Loading message */}
        <div className="text-center mb-4">
          <h3 className="text-lg font-semibold text-gray-800 mb-2">
            {message}
          </h3>
          {showSlowLoadingMessage && (
            <p className="text-sm text-amber-600">
              This is taking longer than usual...
            </p>
          )}
        </div>

        {/* Progress bar container */}
        <div className="relative w-full h-4 bg-gray-200 rounded-full overflow-hidden">
          {/* Animated background shimmer */}
          <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/30 to-transparent animate-shimmer" />
          
          {/* Progress fill */}
          <div
            className="absolute inset-y-0 left-0 bg-gradient-to-r from-blue-500 to-blue-600 rounded-full transition-all duration-500 ease-out"
            style={{ width: `${clampedProgress}%` }}
          >
            {/* Shine effect */}
            <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent animate-shine" />
          </div>
        </div>

        {/* Progress percentage */}
        <div className="mt-3 text-center">
          <span className="text-2xl font-bold text-blue-600">
            {Math.round(clampedProgress)}%
          </span>
        </div>

        {/* Loading dots animation */}
        <div className="flex justify-center items-center mt-4 space-x-2">
          <div className="w-2 h-2 bg-blue-500 rounded-full animate-bounce" style={{ animationDelay: '0ms' }} />
          <div className="w-2 h-2 bg-blue-500 rounded-full animate-bounce" style={{ animationDelay: '150ms' }} />
          <div className="w-2 h-2 bg-blue-500 rounded-full animate-bounce" style={{ animationDelay: '300ms' }} />
        </div>
      </div>
    </div>
  );
};