import React, { useMemo } from 'react';
import type { CourseReportData } from '@/types';
import { DistributionChart } from '@/components/charts';
import { TraineeDataTable } from '@/components/tables';
import { MasterTrainerFilter, SchoolFilter } from '@/components/filters';
import { filterTrainees, processTraineeData } from '@/lib/utils/courseReportUtils';
import { useFilters } from '@/contexts';

// District filter component for individual course view
const DistrictFilterComponent: React.FC<{
  districts: string[];
  value: string | null;
  onChange: (value: string | null) => void;
}> = ({ districts, value, onChange }) => {
  return (
    <div className="flex flex-col gap-1">
      <label className="text-sm font-medium text-gray-700">
        Districts ({districts.length}):
      </label>
      <select
        value={value || ''}
        onChange={(e) => onChange(e.target.value || null)}
        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary focus:border-transparent"
      >
        <option value="">All</option>
        {districts.map(district => (
          <option key={district} value={district}>
            {district}
          </option>
        ))}
      </select>
    </div>
  );
};

interface IndividualCourseViewProps {
  courseReportData: CourseReportData;
  courseName: string;
}

export const IndividualCourseView: React.FC<IndividualCourseViewProps> = ({
  courseReportData,
  courseName,
}) => {
  const { filters, setMasterTrainer, setSchool, setDistrictCode, getAvailableFilterOptions } = useFilters();

  // Get available filter options based on current selections
  const availableOptions = useMemo(() => {
    return getAvailableFilterOptions(courseReportData);
  }, [courseReportData, getAvailableFilterOptions]);

  // Filter trainees based on selected filters
  const filteredTrainees = useMemo(() => {
    return filterTrainees(courseReportData.trainees, {
      masterTrainer: filters.masterTrainer,
      district: filters.districtCode,
      school: filters.school,
    });
  }, [courseReportData.trainees, filters.masterTrainer, filters.districtCode, filters.school]);

  // Process filtered trainees for display
  const processedTrainees = useMemo(() => {
    return processTraineeData(filteredTrainees);
  }, [filteredTrainees]);

  // Recalculate distributions and summary for filtered data
  const filteredStats = useMemo(() => {
    const totalTrainees = filteredTrainees.length;
    
    if (totalTrainees === 0) {
      return {
        completionDistribution: courseReportData.completionDistribution.map(d => ({ ...d, count: 0, percentage: 0 })),
        gradeDistribution: courseReportData.gradeDistribution.map(d => ({ ...d, count: 0, percentage: 0 })),
        summary: {
          totalTrainees: 0,
          averageCompletion: 0,
          averageGrade: 0,
        },
      };
    }

    // Calculate distributions for filtered data
    const completionRates = filteredTrainees.map(t => parseFloat(t.completion_rate));
    const grades = filteredTrainees.map(t => parseFloat(t.final_grade));

    const ranges = [
      { min: 0, max: 9, label: '0-9' },
      { min: 10, max: 19, label: '10-19' },
      { min: 20, max: 29, label: '20-29' },
      { min: 30, max: 39, label: '30-39' },
      { min: 40, max: 49, label: '40-49' },
      { min: 50, max: 59, label: '50-59' },
      { min: 60, max: 69, label: '60-69' },
      { min: 70, max: 79, label: '70-79' },
      { min: 80, max: 89, label: '80-89' },
      { min: 90, max: 100, label: '90-100' },
    ];

    const completionDistribution = ranges.map(range => {
      const count = completionRates.filter(v => v >= range.min && v <= range.max).length;
      return {
        range: range.label,
        count,
        percentage: (count / totalTrainees) * 100,
      };
    });

    const gradeDistribution = ranges.map(range => {
      const count = grades.filter(v => v >= range.min && v <= range.max).length;
      return {
        range: range.label,
        count,
        percentage: (count / totalTrainees) * 100,
      };
    });

    const totalCompletion = completionRates.reduce((sum, v) => sum + v, 0);
    const totalGrade = grades.reduce((sum, v) => sum + v, 0);

    return {
      completionDistribution,
      gradeDistribution,
      summary: {
        totalTrainees,
        averageCompletion: totalCompletion / totalTrainees,
        averageGrade: totalGrade / totalTrainees,
      },
    };
  }, [filteredTrainees, courseReportData]);

  return (
    <div className="space-y-6">
      {/* Filter Section */}
      <div className="bg-white rounded-lg shadow p-6">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <MasterTrainerFilter
            masterTrainers={availableOptions.masterTrainers}
            value={filters.masterTrainer}
            onChange={setMasterTrainer}
          />
          <DistrictFilterComponent
            districts={availableOptions.districts}
            value={filters.districtCode}
            onChange={setDistrictCode}
          />
          <SchoolFilter
            schools={availableOptions.schools}
            value={filters.school}
            onChange={setSchool}
          />
        </div>
      </div>

      {/* Summary Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <div className="bg-white rounded-lg shadow p-6">
          <h3 className="text-sm font-medium text-gray-500 uppercase tracking-wider">
            Total Trainees
          </h3>
          <p className="mt-2 text-3xl font-bold text-gray-900">
            {filteredStats.summary.totalTrainees.toLocaleString()}
          </p>
        </div>
        <div className="bg-white rounded-lg shadow p-6 overflow-hidden">
          <h3 className="text-sm font-medium text-gray-500 uppercase tracking-wider mb-4">
            Average Completion Rate
          </h3>
          <div className="relative">
            <p className="text-3xl font-bold text-gray-900 relative z-10">
              {filteredStats.summary.averageCompletion.toFixed(2)}%
            </p>
            {/* Dynamic progress bar background */}
            <div className="mt-4 h-3 bg-gray-200 rounded-full overflow-hidden">
              <div
                className={`h-full rounded-full transition-all duration-500 ease-out ${
                  filteredStats.summary.averageCompletion < 20
                    ? 'bg-red-500'
                    : filteredStats.summary.averageCompletion < 50
                    ? 'bg-orange-500'
                    : filteredStats.summary.averageCompletion < 80
                    ? 'bg-yellow-500'
                    : 'bg-green-500'
                }`}
                style={{ width: `${Math.min(filteredStats.summary.averageCompletion, 100)}%` }}
              />
            </div>
          </div>
        </div>
        <div className="bg-white rounded-lg shadow p-6">
          <h3 className="text-sm font-medium text-gray-500 uppercase tracking-wider">
            Average Final Grade
          </h3>
          <p className="mt-2 text-3xl font-bold text-gray-900">
            {filteredStats.summary.averageGrade.toFixed(2)}
          </p>
        </div>
      </div>

      {/* Distribution Charts */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <DistributionChart
          title="Completion Rate of Trainees"
          data={filteredStats.completionDistribution}
          color="#3b82f6"
          height={300}
        />
        <DistributionChart
          title="Final Grades of Trainees"
          data={filteredStats.gradeDistribution}
          color="#8b5cf6"
          height={300}
        />
      </div>

      {/* Trainee Data Table */}
      <TraineeDataTable
        trainees={processedTrainees}
        courseName={courseName}
      />
    </div>
  );
};