import React, { useMemo } from 'react';
import Chart from 'react-apexcharts';
import type { ApexOptions } from 'apexcharts';
import { CHART_COLORS, CHART_DEFAULTS } from '@/lib/constants';
import { formatNumber, formatPercentage } from '@/lib/utils';
import type { Totals } from '@/types';
import { toPieData } from '@/lib/utils/transformers';

interface PieChartProps {
  data: Totals;
  height?: number;
}

export const PieChart: React.FC<PieChartProps> = ({ data, height = 350 }) => {
  const chartData = useMemo(() => toPieData(data), [data]);

  // Create labels with numbers
  const labelsWithNumbers = useMemo(() => [
    `${formatNumber(data.certifiedTotal)} Certified`,
    `${formatNumber(data.traineesTotal - data.certifiedTotal)} Not Certified`
  ], [data]);

  const options: ApexOptions = {
    chart: {
      type: 'donut',
      fontFamily: CHART_DEFAULTS.fontFamily,
      toolbar: CHART_DEFAULTS.toolbar,
    },
    labels: labelsWithNumbers,
    colors: [CHART_COLORS.certified, CHART_COLORS.notCertified],
    legend: {
      position: 'bottom',
      fontSize: '14px',
    },
    plotOptions: {
      pie: {
        donut: {
          size: '65%',
        },
      },
    },
    dataLabels: {
      enabled: true,
      formatter: (val: number) => formatPercentage(val),
    },
    tooltip: {
      y: {
        formatter: (val: number) => formatNumber(val),
      },
    },
    responsive: [
      {
        breakpoint: 480,
        options: {
          legend: {
            position: 'bottom',
          },
        },
      },
    ],
  };

  return (
    <Chart
      options={options}
      series={chartData.series}
      type="donut"
      height={height}
    />
  );
};