import type { 
  CourseTraineeRecord, 
  DistributionRange, 
  CourseReportData,
  ProcessedTraineeData 
} from '@/types';

/**
 * Distribution ranges for completion rates and grades
 */
const DISTRIBUTION_RANGES = [
  { min: 0, max: 9, label: '0-9' },
  { min: 10, max: 19, label: '10-19' },
  { min: 20, max: 29, label: '20-29' },
  { min: 30, max: 39, label: '30-39' },
  { min: 40, max: 49, label: '40-49' },
  { min: 50, max: 59, label: '50-59' },
  { min: 60, max: 69, label: '60-69' },
  { min: 70, max: 79, label: '70-79' },
  { min: 80, max: 89, label: '80-89' },
  { min: 90, max: 100, label: '90-100' },
];

/**
 * Calculate distribution of values across ranges
 */
function calculateDistribution(values: number[], total: number): DistributionRange[] {
  const distribution = DISTRIBUTION_RANGES.map(range => {
    const count = values.filter(v => v >= range.min && v <= range.max).length;
    return {
      range: range.label,
      count,
      percentage: total > 0 ? (count / total) * 100 : 0,
    };
  });

  return distribution;
}

/**
 * Calculate completion rate distribution
 */
export function calculateCompletionDistribution(
  trainees: CourseTraineeRecord[]
): DistributionRange[] {
  const completionRates = trainees.map(t => parseFloat(t.completion_rate));
  return calculateDistribution(completionRates, trainees.length);
}

/**
 * Calculate final grade distribution
 */
export function calculateGradeDistribution(
  trainees: CourseTraineeRecord[]
): DistributionRange[] {
  const grades = trainees.map(t => parseFloat(t.final_grade));
  return calculateDistribution(grades, trainees.length);
}

/**
 * Calculate summary statistics
 */
export function calculateSummaryStats(trainees: CourseTraineeRecord[]) {
  const totalTrainees = trainees.length;
  
  if (totalTrainees === 0) {
    return {
      totalTrainees: 0,
      averageCompletion: 0,
      averageGrade: 0,
    };
  }

  const totalCompletion = trainees.reduce(
    (sum, t) => sum + parseFloat(t.completion_rate), 
    0
  );
  const totalGrade = trainees.reduce(
    (sum, t) => sum + parseFloat(t.final_grade), 
    0
  );

  return {
    totalTrainees,
    averageCompletion: totalCompletion / totalTrainees,
    averageGrade: totalGrade / totalTrainees,
  };
}

/**
 * Extract unique filter values from trainee data
 */
export function extractFilterValues(trainees: CourseTraineeRecord[]) {
  const masterTrainers = Array.from(
    new Set(trainees.map(t => t.group_name).filter(Boolean))
  ).sort();

  const districts = Array.from(
    new Set(trainees.map(t => t.district).filter(Boolean))
  ).sort();

  const schools = Array.from(
    new Set(trainees.map(t => t.school).filter(Boolean))
  ).sort();

  return {
    masterTrainers,
    districts,
    schools,
  };
}

/**
 * Process raw trainee records into display format
 */
export function processTraineeData(
  trainees: CourseTraineeRecord[]
): ProcessedTraineeData[] {
  return trainees.map(t => ({
    userId: t.user_id,
    district: t.district,
    school: t.school,
    firstName: t.firstname,
    lastName: t.lastname,
    completion: parseFloat(t.completion_rate),
    finalGrade: parseFloat(t.final_grade),
    masterTrainer: t.group_name,
    email: t.email,
    phone: t.phone1,
    nid: t.nid,
  }));
}

/**
 * Filter trainees by master trainer, district, and school
 */
export function filterTrainees(
  trainees: CourseTraineeRecord[],
  filters: {
    masterTrainer?: string | null;
    district?: string | null;
    school?: string | null;
  }
): CourseTraineeRecord[] {
  let filtered = [...trainees];

  if (filters.masterTrainer) {
    filtered = filtered.filter(t => t.group_name === filters.masterTrainer);
  }

  if (filters.district) {
    filtered = filtered.filter(t => t.district === filters.district);
  }

  if (filters.school) {
    filtered = filtered.filter(t => t.school === filters.school);
  }

  return filtered;
}

/**
 * Process complete course report data
 */
export function processCourseReportData(
  trainees: CourseTraineeRecord[]
): CourseReportData {
  return {
    trainees,
    completionDistribution: calculateCompletionDistribution(trainees),
    gradeDistribution: calculateGradeDistribution(trainees),
    summary: calculateSummaryStats(trainees),
    filters: extractFilterValues(trainees),
  };
}

/**
 * Export trainees data to CSV format
 */
export function exportToCSV(
  trainees: ProcessedTraineeData[],
  filename: string = 'trainees-data.csv'
): void {
  if (trainees.length === 0) {
    console.warn('No data to export');
    return;
  }

  // CSV headers
  const headers = [
    'DISTRICT',
    'SCHOOL',
    'FIRST NAME',
    'LAST NAME',
    'COMPLETION (%)',
    'FINAL GRADE',
    'MASTER TRAINER',
    'EMAIL',
    'PHONE',
    'NID',
  ];

  // CSV rows
  const rows = trainees.map(t => [
    t.district,
    t.school,
    t.firstName,
    t.lastName,
    t.completion.toFixed(2),
    t.finalGrade.toFixed(2),
    t.masterTrainer,
    t.email,
    t.phone,
    t.nid,
  ]);

  // Combine headers and rows
  const csvContent = [
    headers.join(','),
    ...rows.map(row => row.map(cell => `"${cell}"`).join(',')),
  ].join('\n');

  // Create blob and download
  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
  const link = document.createElement('a');
  const url = URL.createObjectURL(blob);
  
  link.setAttribute('href', url);
  link.setAttribute('download', filename);
  link.style.visibility = 'hidden';
  
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  
  URL.revokeObjectURL(url);
}