import React, { useMemo } from 'react';
import Chart from 'react-apexcharts';
import type { ApexOptions } from 'apexcharts';
import { CHART_COLORS, CHART_DEFAULTS } from '@/lib/constants';
import { formatNumber, formatDate } from '@/lib/utils';
import type { TimePoint, ChartDataPoint } from '@/types';

interface AreaChartProps {
  data: TimePoint[];
  seriesName: string;
  yAxisTitle: string;
  height?: number;
  dataTransformer: (data: TimePoint[]) => ChartDataPoint[];
  valueFormatter?: (val: number) => string;
  color?: string;
}

export const AreaChart: React.FC<AreaChartProps> = ({
  data,
  seriesName,
  yAxisTitle,
  height = 350,
  dataTransformer,
  valueFormatter = formatNumber,
  color = CHART_COLORS.primary,
}) => {
  const chartData = useMemo(() => dataTransformer(data), [data, dataTransformer]);

  const options: ApexOptions = {
    chart: {
      type: 'area',
      fontFamily: CHART_DEFAULTS.fontFamily,
      toolbar: CHART_DEFAULTS.toolbar,
      zoom: {
        enabled: false,
      },
    },
    dataLabels: {
      enabled: false,
    },
    stroke: {
      curve: 'smooth',
      width: 2,
    },
    fill: {
      type: 'gradient',
      gradient: {
        shadeIntensity: 1,
        opacityFrom: 0.7,
        opacityTo: 0.3,
        stops: [0, 90, 100],
      },
    },
    xaxis: {
      type: 'datetime',
      labels: {
        formatter: (val: string) => formatDate(val, 'short'),
      },
    },
    yaxis: {
      title: {
        text: yAxisTitle,
      },
      labels: {
        formatter: (val: number) => valueFormatter(val),
      },
    },
    colors: [color],
    tooltip: {
      x: {
        formatter: (val: number) => formatDate(new Date(val).toISOString(), 'long'),
      },
      y: {
        formatter: (val: number) => valueFormatter(val),
      },
    },
    grid: {
      borderColor: '#f1f1f1',
    },
  };

  return (
    <Chart
      options={options}
      series={[{ name: seriesName, data: chartData }]}
      type="area"
      height={height}
    />
  );
};